<template>
  <el-card class="fnsd-tax-card">
    <div>
      <el-button icon="el-icon-back"
                title="Вернуться к таблице ПЕРЕЧЕНЬ НО"
                circle
                style="margin-right: 20px;"
                @click="toTaxPage"
      ></el-button>
      <span class="fnsd-card-title">ПЕРЕЧЕНЬ НАЛОГОПЛАТЕЛЬЩИКОВ</span>
    </div>
    <el-input
      placeholder="Поиск"
      class="fnsd-tax-search"
      prefix-icon="el-icon-search"
      v-model="filter">
      <i slot="suffix" @click="filter=''" title="Очистить" class="el-input__icon el-icon-close" style="cursor: pointer"></i>
    </el-input>
    <el-table
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-table
            :data="props.row.RNPT_DATA.VALUES"
            style="width: 100%"
            @row-click="onRowClick"
          >
            <el-table-column
              prop="IPT"
              label="Регистрационный номер партии"
            ></el-table-column>
            <el-table-column
              prop="TNVED"
              label="ТНВЭД"
            ></el-table-column>
            <el-table-column
              prop="formattedValue"
              :label="currentLegend.text"
            ></el-table-column>
          </el-table>
        </template>
      </el-table-column>
      <el-table-column
        prop="INN"
        label="ИНН"
      ></el-table-column>
      <el-table-column
        prop="FULL_NAME"
        label="Наименование"
      ></el-table-column>
      <el-table-column
        prop="formattedValue"
        :label="currentLegend.text"
      ></el-table-column>
      <el-table-column
        prop="RNPT_COUNT"
        label="Количество партий"
      ></el-table-column>
    </el-table>
  </el-card>
</template>

<script>
  import { mapState, mapGetters } from 'vuex';
  import MapMode from '../map/map-mode';
  import regionInfoService from '../../services/region-info-service';
  import UnifiedClientProxy from '../../helpers/unified-client-proxy';
  import CountryMode from '../map/country-mode';
  import { nvlFloat, orDefault, getLegendByIndex } from '../../helpers/utils';

  export default {
    name: 'TaxpayerPage',
    props: {
      taxCode: {
        type: String,
      },
    },
    data() {
      return {
        valueColumnName: '',
        filter: '',
        isError: false,
        tableData: [],
        columns: {},
        innerColumns: [],
        isEmpty: false,
        visible: false,
      };
    },

    computed: {
      ...mapState({
        legend: state => state.legend,
        dim: state => state.dim,
        currentPeriod: state => state.period,
        indicatorId: state => state.indicatorId,
      }),
      ...mapState('scale', {
        currentScale: 'currentScale',
      }),
      ...mapGetters('scale', {
        scaledValue: 'scaledValue',
      }),

      currentLegend() {
        return getLegendByIndex(this.legend, this.indicatorId);
      },
    },

    watch: {
      currentScale() {
        this.updateFormattedValues();
      },
    },

    created() {
      this.updateTable();
    },

    methods: {
      toTaxPage() {
        this.$emit('map-mode-changed', {
          mode: MapMode.REGION,
        });
      },
      openGraph(params) {
        new UnifiedClientProxy().executeFunction('DashboardManagerScriptModule', 'rnpt', params);
      },
      onRowClick(item) {
        console.log(item);
      },
      updateTable() {
        this.columns = {};
        regionInfoService.getTaxDivisionTableData(
          [this.taxCode],
          'this.period.taxYear',
          'this.period.taxPeriodNumber',
          'countryCode',
          'this.indicator.code',
          'this.taxType.code',
          'this.violationFilter.Code',
          'this.riskFilter.Code',
        ).then((response) => {
          console.log(response);
          this.tableData = response.data.data;
          if (this.tableData.length) {
            this.valueColumnName = `${Object.keys(this.tableData[0])[1]}`;
          }
          // this.tableData.forEach((row) => {
          //   Object.keys(row).forEach((col) => {
          //       const value = row[col];
          //       if (typeof value !== 'object') {
          //         this.initFormattedObject(row, col);
          //       } else {
          //         this.$set(row, 'hasRnptData', true);
          //         this.$set(row, 'expand', false);
          //         if (!this.innerColumns.length && value.VALUES.length) {
          //           Object.keys(value.VALUES[0])
          //             .forEach((key) => {
          //               const col = this.columns[key];
          //               if (col) {
          //                 this.innerColumns.push(col);
          //               } else {
          //                 this.innerColumns.push({ indicatorName: key });
          //               }
          //             });
          //         }
          //         // если количество меньше чем PAGING_DATA.RowsPerPage, то однозначно более данных нет,
          //         // а вот если равно, то узнаем только при подгрузке
          //         const hasMoreData = value.VALUES.length === value.PAGING_DATA.RowsPerPage;
          //         this.$set(row, 'hasMoreData', hasMoreData);
          //         value.VALUES.forEach((item) => {
          //           Object.keys(item).forEach((innerCol) => {
          //             this.initFormattedObject(item, innerCol);
          //           });
          //         });
          //       }
          //     });
          // });
          this.updateFormattedValues();
          this.visible = true;
        })
          .catch((error) => {
            console.log(orDefault(error.response, error));
            this.isError = true;
          });
      },

      async loadMoreIpt(item) {
        item.RNPT_DATA.PAGING_DATA.CurrentPage += 1;
        const countryCode = (this.country && this.country.Code !== CountryMode.ALL) ? this.country.Code : this.eaeuFilter.value;
        const response = await regionInfoService.loadPageTaxpayerTableData(
          this.period.taxYear,
          this.period.taxPeriodNumber,
          countryCode,
          this.indicator.code,
          this.taxType.code,
          this.violationFilter.Code,
          this.riskFilter.Code,
          item.INN.formattedValue,
          item.RNPT_DATA.PAGING_DATA,
        );
        console.log(response);
        if (response.data.length > 0) {
          if (response.data.length < item.RNPT_DATA.PAGING_DATA.RowsPerPage) {
            item.hasMoreData = false;
          }
          response.data.forEach((i) => {
            Object.keys(i).forEach((innerCol) => {
              this.initFormattedObject(i, innerCol);
            });
          });
          this.updateFormattedValues(response.data);
          item.RNPT_DATA.VALUES = item.RNPT_DATA.VALUES.concat(response.data);
        } else {
          item.hasMoreData = false;
        }
      },

      initFormattedObject(value, column) {
        const innerValue = value[column];
        value[column] = {};
        value[column].formattedValue = orDefault(innerValue, '');
        if (this.columns[column] && this.columns[column].dimVisible) {
          value[column].indicatorValue = nvlFloat(innerValue);
        }
      },

      updateFormattedValues(data) {
        const rows = data || this.tableData;
        rows.forEach((row) => {
          console.log(row);
          const col = row[this.valueColumnName];
          row.formattedValue = this.scaledValue(col);
          row.RNPT_DATA.VALUES.forEach((item) => {
            const value = item[this.valueColumnName];
            item.formattedValue = this.scaledValue(value);
          });
        });
        // todo не обновляется значение в таблице без манипуляций
        this.tableData.push({});
        this.tableData.pop();
      },

    },
  };
</script>
