import { formattedValue } from '../../../helpers/utils';

const thousands = { coefficient: 1, name: 'тысячи', shortName: 'тыс.', position: 33, power: 0 };
const millions = { coefficient: 1000, name: 'миллионы', shortName: 'млн', position: 66, power: 1 };
const billions = { coefficient: 1000000, name: 'миллиарды', shortName: 'млрд', position: 100, power: 2 };

const scaleState = {
  scales: [
    thousands,
    millions,
    billions,
  ],
  currentScale: thousands,
};

const scaleGetters = {
  scaledValue: state => value => `${formattedValue(value / state.currentScale.coefficient)} ${state.currentScale.shortName}`,
};

const scaleMutations = {
  setThousands(state) {
    state.currentScale = thousands;
  },
  setMillions(state) {
    state.currentScale = millions;
  },
  setBillions(state) {
    state.currentScale = billions;
  },
};

const scaleActions = {};

export const scaleModule = {
  namespaced: true,
  state: scaleState,
  getters: scaleGetters,
  mutations: scaleMutations,
  actions: scaleActions,
};
