<template>
  <fragment>
    <div class="text-center">
      <region-map :loading="loading"
                  :features="geometry"
                  :active-region="activeRegion"
                  :items="items"
                  :projection="projection"
                  @onmouseenter="onMouseEnter"
                  @click="regionSelected"
      ></region-map>
    </div>
    <tooltip
      :items="items"
      :loading="loading"
      :active-region="activeRegion"
      tooltip-class="fnsd-map-attr-level-1"
      @onmouseenter="onMouseEnter"
      @click="regionSelected"
    ></tooltip>
  </fragment>
</template>

<script>
  import { mapState, mapMutations, mapGetters } from 'vuex';
  import Map from '../../components/map/Map.vue';
  import projection from '../../helpers/projection';
  import federalDistrictInfoService from '../../services/federal-district-info-service';
  import russiaInfoService from '../../services/russia-info-service';
  import regionInfoService from '../../services/region-info-service';
  import MapMode from './map-mode';
  import { nvlFloat, loadRegions, updateColorMap, updateFormattedValues, getLegendByIndex } from '../../helpers/utils';
  import Tooltip from '../../components/map/Tooltip.vue';

  export default {
    name: 'app',
    components: {
      'region-map': Map,
      Tooltip,
    },
    props: {
      federalDistrict: {
        type: Object,
        required: false,
      },
    },
    data() {
      return {
        activeRegion: null,
        isError: false,
        items: {},
        itemsRF: [],
        loading: false,
      };
    },
    computed: {
      projection() {
        return projection.centeredFeature({
          type: 'FeatureCollection',
          features: this.geometry,
        });
      },
      ...mapState('geometry', {
        geometryL1: state => state.districts,
        geometryL2: state => state.regions,
      }),
      ...mapState({
        level: state => state.level,
        legend: state => state.legend,
        period: state => state.period,
        indicatorId: state => state.indicatorId,
      }),
      ...mapState('scale', {
        currentScale: 'currentScale',
      }),
      ...mapGetters('scale', {
        scaledValue: 'scaledValue',
      }),
      currentLegend() {
        return getLegendByIndex(this.legend, this.indicatorId);
      },
      isRFLevel() {
        return this.level === 1;
      },
      geometry() {
        return this.isRFLevel ? this.geometryL1 : this.geometryL2.filter(r => r.properties.foId === this.federalDistrict.foId);
      },
    },
    created() {
      this.loadInfo();
    },
    watch: {
      level() {
        this.loadInfo();
      },
      currentScale() {
        this.updateFormattedValues();
      },
      indicatorId() {
        this.updateFormattedValues();
      },
    },
    methods: {
      ...mapMutations({
        setLegend: 'SET_LEGEND',
        setLevel: 'SET_LEVEL',
      }),

      onMouseEnter(id) {
        this.activeRegion = id;
      },

      regionSelected(info) {
        console.log(info);
        if (this.isRFLevel) {
          if (info.toRegionPage) {
            this.$emit('map-mode-changed', {
              mode: MapMode.REGION,
            });
          } else {
            this.activeRegion = null;
            this.$emit('map-mode-changed', {
              mode: MapMode.FEDERAL_DISTRICT,
              props: {
                federalDistrict: info,
              },
            });
          }
        } else {
          this.$emit('map-mode-changed', {
            mode: MapMode.REGION,
          });
        }
      },

      async loadInfo() {
        this.items = {};
        this.loading = true;
        const key = this.isRFLevel ? 'foId' : 'regionId';
        const regions = [];
        this.geometry.forEach((feature) => {
          regions.push(feature.properties[key]);
          this.$set(this.items, feature.properties[key].toString(), []);
        });
        try {
          const response = this.isRFLevel
            ? await federalDistrictInfoService.getFederalDistrictsInfoLevel(
              regions,
              this.period,
              'countryCode',
              'this.isTemp',
              'this.taxType.code',
              'this.violationFilter.Code',
              'this.riskFilter.Code',
            )
            : await regionInfoService.getRegionInfo(
              regions,
              this.period,
              'countryCode',
              'this.taxType.code',
              'this.violationFilter.Code',
              'this.riskFilter.Code',
            );
          console.log(response);
          this.setLegend(loadRegions(response, this.items, this.geometry, key));

          await this.loadTotalValues('countryCode');
          this.updateFormattedValues();
          this.loading = false;
        } catch (error) {
          console.log(error);
          this.isError = true;
        }
      },

      updateFormattedValues() {
        updateFormattedValues(this.items, this.legend, this.indicatorId, { id: this.currentScale.power, shortName: this.currentScale.shortName });
      },

      async loadTotalValues(countryCode) {
        const response = this.isRFLevel
          ? await russiaInfoService.getRussiaInfo(
          this.period,
          'this.isTemp',
          countryCode,
          'this.taxType.code',
          'this.violationFilter.Code',
          'this.riskFilter.Code',
        )
          : await federalDistrictInfoService.getFederalDistrictsInfoLevel(
            [this.federalDistrict.foId],
            this.period,
            'country',
            false,
            'this.taxType.code',
            'this.violationFilter.Code',
            'this.riskFilter.Code',
          );
        console.log(response);
        this.itemsRF = [];
        Object.keys(response.data)
          .forEach((key, i) => {
            const item = this.isRFLevel ? response.data[key] : response.data[key][this.federalDistrict.foId];
            this.fillItem(item, i);
            this.itemsRF.push(item);
          });
        updateColorMap(this.items, this.itemsRF);
      },

      fillItem(item, i) {
        item.indicatorValue = nvlFloat(item.indicatorValue);
        if (getLegendByIndex(this.legend, i).dimVisible) {
          item.indicatorUnit = item.indicatorUnit.replace('тыс.', '').trim();
          item.formattedUnit = `${this.currentScale.shortName}`;
          item.formattedValue = this.scaledValue(item.indicatorValue);
        } else {
          item.formattedUnit = '';
          item.formattedValue = Object.assign(item.indicatorValue || 0);
        }
      },

    },
  };
</script>
