// configure language
locale.use(lang);

Vue.mixin({
  methods: {
    replaceAll(str, search, replacement) {
      return str.split(search).join(replacement);
    },
    orDefault(value, defaultValue) {
      return orDefault(value, defaultValue);
    },
  },
});

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
