import BaseService from './base-service';

class PieChartService extends BaseService {
  getPieChartData(taxPeriod) {
    const params = {
      taxPeriod,
    };
    return this.$http.post('/GetPieCharts', params);
  }

  getTnvedCharts(filter) {
    return this.$http.post('GetTnvedCharts', filter).then(response => response.data);
  }

  getCountryCharts(filter) {
    return this.$http.post('/GetCountryCharts', filter).then(response => response.data);
  }

  getEnvironmentCharts(filter) {
    return this.$http.post('/GetEnvironmentCharts', filter).then(response => response.data);
  }

  getEaesCharts(filter) {
    return this.$http.post('/GetEaesCharts', filter).then(response => response.data);
  }

  getDropoutReasons(filter) {
    return this.$http.post('/GetReasonsDropoutCharts', filter).then(response => response.data);
  }

  getTaxModes(filter) {
    return this.$http.post('/GetTaxModesCharts', filter).then(response => response.data);
  }

  getRisks(filter) {
    return this.$http.post('/GetRiskCharts', filter).then(response => response.data);
  }

  getViolations(filter) {
    return this.$http.post('/GetViolationCharts', filter).then(response => response.data);
  }

  getConflicts(filter) {
    return this.$http.post('/GetConflictCharts', filter).then(response => response.data);
  }

  getPenalties(filter) {
    return this.$http.post('/GetPenaltyCharts', filter).then(response => response.data);
  }

  getResolvedConflicts(filter) {
    return this.$http.post('/GetClosedConflictCharts', filter).then(response => response.data);
  }

  getRnptCount(filter) {
    return this.$http.post('/GetRnptCntCharts', filter).then(response => response.data);
  }
}

const pieChartService = new PieChartService();

export default pieChartService;
