import IndicatorCode from '../../../core/indicator-codes';

const tnved = { title: 'ТНВЭД', component: 'Tnved' };
const country = { title: 'СТРАНЫ', component: 'Country' };
const environment = { title: 'ЧИСТОТА СРЕДЫ', component: 'Environment' };
const eaes = { title: 'СООТНОШЕНИЕ ЕАЭС И НЕ ЕАЭС', component: 'Eaes' };
const dropoutReasons = { title: 'ПРИЧИНЫ ВЫБЫТИЯ', component: 'DropoutReasons' };
const taxModes = { title: 'РЕЖИМЫ', component: 'TaxModes' };
const violations = { title: 'РАСХОЖДЕНИЯ', component: 'Violations' };
const risks = { title: 'РИСКИ', component: 'Risks' };
const conflicts = { title: 'ПРОТИВОРЕЧИЯ', component: 'Conflicts' };
const penalties = { title: 'ШТРАФЫ', component: 'Penalties' };
const conflictsResolved = { title: 'УСТРАНЕНО ПРОТИВОРЕЧИЙ', component: 'ConflictsResolved' };
const rnptCount = { title: 'ВЫДАНО РНПТ', component: 'RnptCount' };

const diagramsState = {
  diagrams: {
    [IndicatorCode.D_IMPORT]: [tnved, country, environment, eaes],
    [IndicatorCode.D_EXPORT]: [tnved, country, environment, eaes],
    [IndicatorCode.D_EXIT]: [tnved, dropoutReasons],
    [IndicatorCode.REGISTERED_BALANCE]: [tnved],
    [IndicatorCode.CIRCULATION]: [tnved],
    [IndicatorCode.IMPORTERS]: [tnved, taxModes, environment, eaes],
    [IndicatorCode.EXPORTERS]: [tnved, taxModes, environment, eaes],
    [IndicatorCode.DISTRIBUTION]: [tnved],
    [IndicatorCode.RETAIL]: [tnved],
    [IndicatorCode.COUNT_VIOLATIONS]: [tnved, violations],
    [IndicatorCode.COUNT_RISKS]: [tnved, risks],
    [IndicatorCode.D_DOC_REQUEST]: [violations, risks],
    [IndicatorCode.CONFLICT]: [tnved, conflicts],
    [IndicatorCode.PENALTY]: [penalties],
    [IndicatorCode.NOTIFY]: [conflicts, conflictsResolved],
    [IndicatorCode.NOTICE_REG_B]: [tnved, rnptCount],
    [IndicatorCode.NOTICE_REG_I]: [tnved, rnptCount],
  },
};

const diagramsGetters = {
  visibleDiagrams(state, getters, rootState) {
    const { currentIndicator } = rootState.indicators;
    // TODO убрать заглушку [tnved, country, environment]
    const diagrams = state.diagrams[currentIndicator.code] || [tnved, country, environment];
    // Свойство key добавляется к объекту для того, чтобы форсировать пересоздание компонентов диаграмм при смене показателя
    return diagrams.map((diagram, index) => ({ ...diagram, key: new Date().getTime() + index }));
  },
};

const diagramsMutations = {};

const diagramsActions = {};

export const diagramsModule = {
  namespaced: true,
  state: diagramsState,
  getters: diagramsGetters,
  mutations: diagramsMutations,
  actions: diagramsActions,
};
