<template>
  <div>
    <div
      v-for="indicator in indicators"
      :key="indicator.code"
      class="fnsd-indicator-menu-label"
      :class="{
        'fnsd-indicator-menu-item-active': indicator.code === currentIndicator.code,
        'fnsd-indicator-menu-item': !!indicator.code,
      }"
      @click="setCurrentIndicator(indicator)"
    >
      <span>{{indicator.name}}</span>
      <indicator-menu-item :indicators="indicator.children" @set:indicator="$emit('set:indicator', $event)"></indicator-menu-item>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex';

  export default {
    name: 'IndicatorMenuItem',
    props: {
      indicators: {
        type: Array,
        required: true,
      },
    },
    computed: {
      ...mapState('indicators', {
        currentIndicator: 'currentIndicator',
      }),
    },
    methods: {
      setCurrentIndicator(indicator) {
        if (indicator.code) {
          this.$emit('set:indicator', indicator);
        }
      },
    },
  };

</script>

