/**
 * Класс взаимодействия с клиентом кпи
 */
/* eslint-disable */
export default class UnifiedClientProxy {
  async executeFunction(moduleName, functionName, callContext) {
    await CefSharp.BindObjectAsync('unifiedClientIntegration');
    return await unifiedClientIntegration.executeFunction(moduleName, functionName, callContext);
  }
}
