import $http from './axios-instance';

export default class BaseService {
  constructor() {
    this.$http = $http;
  }

  get(path, successCallback = response => response, errorCallback = error => error) {
    return this.$http
      .get(path)
      .then(successCallback, errorCallback);
  }

  post(path, params, successCallback = response => response, errorCallback = error => error) {
    return this.$http
      .post(path, params)
      .then(successCallback, errorCallback);
  }
}
