<template>
  <div class="fnsd-header-tags">
    <el-tag v-for="filter in currentFilters"
            :key="filter.code"
            effect="plain"
            :closable="true"
            @close="removeFilter(filter.code)"
    >{{filter.name}}</el-tag>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex';

  export default {
    name: 'FilterPanel',

    computed: {
      ...mapGetters('filter', {
        currentFilters: 'currentFilters',
      }),
    },

    methods: {
      ...mapMutations('filter', {
        removeFilter: 'removeFilter',
      }),
    },
  };
</script>

