import groupBy from 'lodash-es/groupBy';

// 1000000 => 1 000 000
export function formattedValue(value) {
  return value.toLocaleString('ru');
}

export function orDefault(value, defaultValue) {
  return value || defaultValue;
}

/**
 * парсить число, если не смогли - 0
 */
export function nvlInt(input) {
  const result = parseInt(input, 10);
  return orDefault(result, 0);
}

export function nvlFloat(input) {
  const result = parseFloat(input);
  return orDefault(result, 0);
}

// Коды оттенков синего, которые отмечены на карте РФ (от самого темного к более светлому):
const mapColors = [
  '13,79,160',
  '26,89,165',
  '38,98,170',
  '51,107,175',
  '64,116,180',
  '77,126,185',
  '89,135,190',
  '102,144,195',
  '115,153,200',
  '128,163,205',
  '140,172,210',
  '153,181,215',
  '179,200,225',
  '191,209,230',
  '204,218,235',
  '217,227,240',
  '230,237,245',
];

/**
 * заполнить регионы цветами
 * @param array - массив значений
 * @param min - минимальное процентное значение
 * @param keyColor - имя параметра куда записать код цвета
 * @param keyLength - имя параметра со значением процента
 */
export function fitColor(array, min = 0, keyColor = 'regionColor', keyLength = 'indicatorLength') {
  if (array.length && !array[0]) return;
  const uniqueItems = Array.from(new Set(array.map(x => x[keyLength]).sort((a, b) => b - a)));
  if (uniqueItems.length === 1 && array[0][keyLength] === min) {
    // Самый яркий цвет - слишком яркий, поэтому половиним цвета
    const light = mapColors[Math.round(mapColors.length / 2)];

    // Если только нули, делаем светлую карту
    array.forEach((ind) => {
      ind[keyColor] = light;
    });
  } else {
    // Считаем шаг.
    // TA-1697 Для отображения раскраски регионов, где кол-во областей не превышает 9 (например, Южный, Северо-Кавказский, Уральский)
    // - рекомендуется использовать цвета через один (так будет заметнее разница в оттенках).
    let step = mapColors.length / uniqueItems.length;
    step = step < 1 ? 1 : step;

    // Ключ - длина, значение - индекс в списке цветов
    const dct = {};
    uniqueItems.map((v, idx) => (dct[v] = (idx * step) / mapColors.length * 100));

    array.forEach((ind) => {
      const dctValue = Math.round(dct[ind[keyLength]] / 100 * mapColors.length);
      ind[keyColor] = mapColors[dctValue];
    });
  }
}

/**
 * заполнить проценты array / total
 * @param array - исходный массив значений
 * @param total - значение - всего
 * @param keyValue - имя параметра со значением
 * @param keyLength - имя параметра куда записать процент
 * @param min - минимальное процентное значение
 */
export function fitPercentage(array, total, min, keyValue = 'indicatorValue', keyLength = 'indicatorLength') {
  for (let i = 0; i < array.length; i++) {
    if (!array[i]) return;
    array[i][keyLength] = Math.round(parseFloat(array[i][keyValue]) / parseFloat(total[keyValue]) * 100);
    if (Number.isNaN(array[i][keyLength]) || (min && array[i][keyLength] < min)) {
      array[i][keyLength] = min;
    }
  }
}

/**
 * Заполнение информации показателей регионов
 * @param response
 * @param items - массив показателей
 * @param geometry - массив статических данных о регионах/фо
 * @param regionKey - regionId для уровня регионов, foId для уровня округов
 * @returns {[]} - легенда с группировкой по groupName
 */
export function loadRegions(response, items, geometry, regionKey) {
  const result = [];
  const legend = [];
  Object.keys(response.data).forEach((key, idx) => {
    legend.push({
      text: key,
      orderBy: response.data[key].orderBy,
      dimVisible: response.data[key].dimVisible,
      countryVisible: response.data[key].countryVisible,
      taxTypeVisible: response.data[key].taxTypeVisible,
      violationVisible: response.data[key].violationVisible,
      riskVisible: response.data[key].riskVisible,
      code: response.data[key].code,
      groupName: response.data[key].groupName,
      index: idx,
    });

    geometry.forEach((feature) => {
      const item = response.data[key][feature.properties[regionKey]];
      if (response.data[key].dimVisible) {
        item.indicatorUnit = item.indicatorUnit.replace('тыс.', '').trim();
      }
      item.formattedUnit = '';
      item.formattedValue = Object.assign(orDefault(item.indicatorValue, 0));
      item.indicatorValue = nvlFloat(item.indicatorValue);
      item.indicatorLength = '';
      item.regionColor = '';
      item.regionName = feature.properties.name;
      items[feature.properties[regionKey].toString()].push(item);
    });
  });
  const groupLegend = groupBy(legend, 'groupName');
  for (const [key, value] of Object.entries(groupLegend)) {
    const item = {
      expand: !key,
      data: value,
      groupName: key,
    };
    result.push(item);
  }

  return result;
}

export function getLegendByIndex(legend, index) {
  let result = {};
  legend.forEach((group) => {
    group.data.forEach((indicator) => {
      if (indicator.index === index) {
        result = indicator;
      }
    });
  });
  return result;
}

/**
 * Обновление полей значения и ед. измерения при изменении фильтра размерность
 * @param items - массив показателей
 * @param legend - легенда
 * @param id - текущий индекс в легенде
 * @param dim - размерность
 */
export function updateFormattedValues(items, legend, id, dim) {
  Object.values(items)
    .forEach((value) => {
      value.forEach((x, i) => {
        const currLegend = getLegendByIndex(legend, i);
        if (currLegend && currLegend.dimVisible) {
          x.formattedValue = formattedValue(value[id].indicatorValue / Math.pow(1000, dim.id));
          x.formattedUnit = `${dim.shortName}`;
        }
      });
    });
}

/**
 * Заполнить информацией о процентном соотношении и цвете
 * @param items
 * @param totalItems
 */
export function updateColorMap(items, totalItems) {
  for (let i = 0; i < totalItems.length; i++) {
    fitPercentage(Object.keys(items).map(key => items[key]).map(region => region[i]), totalItems[i], 1);
    fitColor(Object.keys(items).map(key => items[key]).map(region => region[i]), 1);
  }
}

export function createLabels(svgElem, labelTopText, labelBotText) {
  const xmlns = 'http://www.w3.org/2000/svg';
  const g = document.createElementNS(xmlns, 'g');
  const labels = svgElem.querySelector('.apexcharts-datalabels-group');
  if (!labels) return;
  const line = document.createElementNS(xmlns, 'line');
  const labelTop = document.createElementNS(xmlns, 'text');
  const labelBot = document.createElementNS(xmlns, 'text');
  const x1 = parseFloat(labels.childNodes[0].attributes.x.value);
  const x2 = parseFloat(labels.childNodes[1].attributes.x.value);
  const y1 = parseFloat(labels.childNodes[0].attributes.y.value);
  const y2 = parseFloat(labels.childNodes[1].attributes.y.value);
  line.setAttribute('x1', x1 - 40);
  line.setAttribute('x2', x1 + 40);
  line.setAttribute('y1', y1 + 5);
  line.setAttribute('y2', y1 + 5);
  line.setAttribute('stroke', 'black');
  labelTop.setAttributeNS(null, 'text-anchor', 'middle');
  labelTop.setAttributeNS(null, 'font-size', '16');
  labelTop.setAttributeNS(null, 'x', x1);
  labelTop.setAttributeNS(null, 'y', y1 - 10);
  labelBot.setAttributeNS(null, 'text-anchor', 'middle');
  labelBot.setAttributeNS(null, 'font-size', '18');
  labelBot.setAttributeNS(null, 'x', x2);
  labelBot.setAttributeNS(null, 'y', y2);

  labelTop.innerHTML = labelTopText;
  labelBot.innerHTML = labelBotText;
  g.appendChild(line);
  g.appendChild(labelTop);
  g.appendChild(labelBot);
  labels.parentNode.appendChild(g);
}
