<template>
  <fragment>
    <div v-if="chartMode" class="fnsd-chart-panel">
      <div class="fnsd-chart-mode">
        <img src="../../images/button-chart-pressed.png">
        <img @click="chartMode = false" src="../../images/button-list.png" class="img-active">
      </div>

      <div>
        <clickable-chart
          label-top="ВЫДАНО"
          label-bottom="РНПТ"
          height="370px"
          :series="series"
          :labels="labels"
          :colors="colors"
          :selected="selectedRnptName"
          @slice:select="updateRnptSelection"
        ></clickable-chart>
      </div>
      <div class="fnsd-chart-radio fnsd-chart-radio-compact">
        <el-radio-group v-model="radio"
                        class="fnsd-chart-radio-count-3"
        >
          <el-radio-button label="Все"></el-radio-button>
          <el-radio-button label="Выдано"></el-radio-button>
          <el-radio-button label="Отклонено"></el-radio-button>
        </el-radio-group>
      </div>

      <div class="fnsd-chart-legend-space-evenly">
        <div
          class="fnsd-chart-legend-item"
          v-for="piece in items"
          :key="piece.pieceText"
        >
          <div class="fnsd-chart-legend-item-inner"
                :style="`opacity: ${ rnpt ? (rnpt.pieceText === piece.pieceText ? 1 : 0.3) : 1}; cursor: pointer;`"
                @click="setRnpt(piece)"
          >
            <card direction="horizontalAlt"
              :title="piece.pieceText"
              :color="piece.color"
              :percentage="piece.pieceSize"
              :value="piece.formattedValue"
            ></card>
          </div>
        </div>
      </div>
    </div>

    <div v-else class="fnsd-chart-panel fnsd-chart-eaes">
      <div class="fnsd-chart-mode">
        <img @click="chartMode = true" src="../../images/button-chart.png" class="img-active">
        <img src="../../images/button-list-pressed.png">
      </div>
      <div class="fnsd-chart-radio fnsd-chart-radio-compact">
        <el-radio-group v-model="radio"
                        class="fnsd-chart-radio-count-3"
        >
          <el-radio-button label="Все"></el-radio-button>
          <el-radio-button label="Выдано"></el-radio-button>
          <el-radio-button label="Отклонено"></el-radio-button>
        </el-radio-group>
      </div>
      <div class="fnsd-chart-table fnsd-chart-table-large">
        <el-table
          :show-header="false"
          :data="tableData"
          height="340"
          class="custom-table"
        >
          <el-table-column width="7px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-point"
                    :style="{'background-color': `${scope.row.color||'#FFF'}`}"
              ></span>
            </template>
          </el-table-column>

          <el-table-column prop="pieceText" width="190px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-title" :style="{'color': `${scope.row.color||'#000'}`}">{{scope.row.pieceText}}</span>
            </template>
          </el-table-column>

          <el-table-column prop="pieceSize" width="135px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-value fnsd-chart-table-separate">{{scope.row.pieceSize}}<span>%</span></span>
            </template>
          </el-table-column>

          <el-table-column prop="formattedValue">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-value">{{scope.row.formattedValue}}<span><!--тут ед. измерения--></span></span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </fragment>
</template>

<script>
  import { mapState, mapGetters, mapMutations, mapActions } from 'vuex';
  import Card from '../../components/Card.vue';
  import ClickableChart from './ClickableChart.vue';
  import FilterCode from '../../core/filter-codes';

  export default {
    name: 'RnptCount',
    components: { Card, ClickableChart },
    data() {
      return {
        chartMode: true,
      };
    },
    computed: {
      ...mapState({
        period: 'period',
      }),

      ...mapGetters('scale', {
        scaledValue: 'scaledValue',
      }),
      ...mapGetters('filter', {
        region: 'region',
        tnved: 'tnved',
        rnpt: 'rnpt',
        serverFilter: 'serverFilter',
      }),

      ...mapGetters('rnpt', {
        items: 'items',
        series: 'series',
        labels: 'labels',
        colors: 'colors',
      }),

      selectedRnptName() {
        return this.rnpt ? this.rnpt.pieceText : null;
      },

      tableData() {
        if (this.radio === 'Все') {
          return this.items;
        }
        return this.items.filter(i => i.pieceText === this.radio);
      },

      radio: {
        get() {
          if (!this.rnpt) {
            return 'Все';
          }
          return this.rnpt.pieceText;
        },
        set(value) {
          if (value === 'Все') {
            this.removeFilter(FilterCode.RNPT_COUNT);
          } else {
            this.setRnpt(this.items.find(piece => piece.pieceText === value));
          }
        },
      },
    },
    created() {
      this.loadItems(this.serverFilter);
      this.$watch(vm => [vm.period, vm.region, vm.tnved].join(), () => this.loadItems(this.serverFilter));
    },
    methods: {
      ...mapMutations('filter', {
        setRnpt: 'setRnpt',
        removeFilter: 'removeFilter',
      }),

      ...mapActions('rnpt', {
        loadItems: 'loadItems',
      }),

      updateRnptSelection(name) {
        const rnpt = this.items.find(i => i.pieceText === name);
        if (rnpt) {
          this.setRnpt(rnpt);
        }
      },
    },
  };
</script>
