import rgbToHex from '../../../../helpers/rgb-helpers';

export const createTop5Module = (service, loadMethod) => ({
  namespaced: true,

  state() {
    return {
      items: [],
    };
  },

  getters: {
    sortedItems(state, getters, rootState, rootGetters) {
      const result = state.items.map(item => ({
        ...item,
        color: rgbToHex(item.pieceColor),
        formattedValue: rootGetters['scale/scaledValue'](item.pieceValue),
      }));
      return result.sort((a, b) => b.pieceValue - a.pieceValue);
    },

    top5(state, getters) {
      return getters.sortedItems.slice(0, 5);
    },
    others(state, getters) {
      return getters.sortedItems.slice(5);
    },
    othersValues(state, getters) {
      const { others } = getters;
      if (others.length > 0) {
        return {
          pieceSize: others.reduce((a, { pieceSize }) => a + pieceSize, 0),
          pieceValue: others.reduce((a, { pieceValue }) => a + pieceValue, 0),
          codes: others.map(piece => piece.pieceCode),
        };
      }
      return null;
    },

    legend(state, getters, rootState, rootGetters) {
      const result = getters.top5.map(item => ({ ...item, codes: [item.pieceCode] }));
      const { othersValues } = getters;
      if (othersValues) {
        result.push({
          color: rgbToHex('255,201,89'),
          pieceColor: '255,201,89',
          pieceSize: othersValues.pieceSize,
          pieceValue: othersValues.pieceValue,
          pieceText: 'Прочие',
          pieceCode: 'Прочие',
          codes: othersValues.codes,
          formattedValue: rootGetters['scale/scaledValue'](othersValues.pieceValue),
        });
      }
      return result;
    },

    series(state, getters) {
      const result = getters.top5.map(piece => piece.pieceSize);
      if (getters.othersValues) {
        result.push(getters.othersValues.pieceSize);
      }
      return result;
    },
    labels(state, getters) {
      const result = getters.top5.map(piece => piece.pieceText);
      if (getters.othersValues) {
        result.push('Прочие');
      }
      return result;
    },
    colors(state, getters) {
      const result = getters.top5.map(piece => piece.color);
      if (getters.othersValues) {
        result.push(rgbToHex('255,201,89'));
      }
      return result;
    },
  },

  mutations: {
    setItems(state, items) {
      state.items = items;
    },
  },

  actions: {
    async loadItems({ commit }, filter) {
      const data = await service[loadMethod](filter);
      commit('setItems', data.chartPieces);
    },
  },
});
