import BaseService from './base-service';

class RussiaInfoService extends BaseService {
  getRussiaInfo(taxPeriod, isTemporary, countryCode, taxType, violationCode, riskCode) {
    const params = {
      isTemporary,
      taxPeriod,
      countryCode,
      taxType,
      violationCode,
      riskCode,
    };
    return this.$http.post('/GetCountryIndicators', params);
  }
}

const russiaInfoService = new RussiaInfoService();

export default russiaInfoService;
