<template>
  <div :class="tooltipClass" style="max-height: 290px; overflow-y: auto; overflow-x: hidden;">
    <div class="fnsd-map-attr-item"
         v-for="(item, idx) in sortedItems"
         :key="idx"
         :style="`opacity: ${activeRegion === item.regionId ? 1 : activeRegion ? 0.3 : 1}`"
         @mouseenter="onMouseEnter(item)"
         @mouseleave="onMouseLeave"
         @click="onClick(item)"
    >
      <div class="fnsd-map-attr-label" style="width: 170px">{{item.regionName}}</div>
      <div class="fnsd-map-attr-bar" :style="calculateStyle(item)">
        <div class="fnsd-bar-value">{{item.formattedValue}} {{item.formattedUnit}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters, mapState } from 'vuex';

export default {
  name: 'Tooltip',
  props: {
    title: {
      type: String,
      required: false,
    },
    items: {
      type: Object,
      required: true,
    },
    loading: {
      type: Boolean,
      required: false,
      default: false,
    },
    activeRegion: {
      type: String,
      required: false,
    },
    tooltipClass: {
      type: String,
      required: true,
    },
  },

  computed: {
    ...mapState({
      indicatorId: state => state.indicatorId,
    }),

    ...mapGetters('geometry', {
      regionById: 'regionById',
    }),

    sortedItems() {
      const items = Object.entries(this.items).map(([i, r]) => {
        const item = r[this.indicatorId];
        if (!item) return {};
        item.regionId = i;
        return item;
      });
      return items.sort((a, b) => b.indicatorValue - a.indicatorValue);
    },
  },

  methods: {
    calculateStyle(item) {
      const width = `width: calc(${item.indicatorLength}% - ${item.indicatorLength / 100}*300px)`;
      return item.regionColor ? `${width}; background-color: rgb(${item.regionColor})` : width;
    },

    onMouseEnter(item) {
      this.$emit('onmouseenter', item.regionId);
    },

    onMouseLeave() {
      this.onMouseEnter({});
    },

    onClick(item) {
      const id = parseInt(item.regionId, 10);
      const info = {
        regionId: id,
        name: item.regionName,
        foId: id,
      };
      this.$emit('click', info);
    },
  },
};
</script>
