<template>
  <fragment>
    <div v-if="chartMode" class="fnsd-chart-panel fnsd-chart-panel-environment">
      <div class="fnsd-chart-mode">
        <img src="../../images/button-chart-pressed.png">
        <img @click="chartMode = false" src="../../images/button-list.png" class="img-active">
      </div>

      <div>
        <clickable-chart
          height="350px"
          label-top="ЧИСТОТА"
          label-bottom="СРЕДЫ"
          :series="series"
          :labels="labels"
          :colors="colors"
          :selected="selectedEnvironmentName"
          @slice:select="updateEnvironmentSelection"
        ></clickable-chart>
      </div>

      <div class="fnsd-chart-radio fnsd-chart-body">
        <el-radio-group v-model="environmentRadio"
                        class="fnsd-chart-radio-count-4"
        >
          <el-radio-button label="Все"></el-radio-button>
          <el-radio-button label="Без риска"></el-radio-button>
          <el-radio-button label="Риски"></el-radio-button>
          <el-radio-button label="Расхождения"></el-radio-button>
        </el-radio-group>
      </div>

      <div class="fnsd-chart-legend-space-evenly fnsd-chart-body">
        <div
          class="fnsd-chart-legend-item"
          v-for="environment in items"
          :key="environment.pieceText"
        >
          <div
            class="fnsd-chart-legend-item-inner"
            :style="`opacity: ${selectedEnvironment ? (selectedEnvironment.pieceText === environment.pieceText ? 1 : 0.3) : 1}; cursor: pointer;`"
            @click="setEnvironment(environment)"
          >
            <card direction="vertical"
                  :title="environment.pieceText"
                  :color="environment.color"
                  :percentage="environment.pieceSize"
                  :value="environment.formattedValue"
            ></card>
          </div>
        </div>
      </div>
    </div>

    <div v-else class="fnsd-chart-panel fnsd-chart-environment">
      <div class="fnsd-chart-mode">
        <img @click="chartMode = true" src="../../images/button-chart.png" class="img-active">
        <img src="../../images/button-list-pressed.png">
      </div>

      <div class="fnsd-chart-radio">
        <el-radio-group v-model="environmentRadio"
                        class="fnsd-chart-radio-count-4"
        >
          <el-radio-button label="Все"></el-radio-button>
          <el-radio-button label="Без риска"></el-radio-button>
          <el-radio-button label="Риски"></el-radio-button>
          <el-radio-button label="Расхождения"></el-radio-button>
        </el-radio-group>
      </div>

      <div class="fnsd-chart-table fnsd-chart-table-large">
        <el-table
          :show-header="false"
          :data="tableData"
          height="340"
          class="custom-table"
        >
          <el-table-column width="7px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-point"
                    :style="{'background-color': `${scope.row.color||'#FFF'}`}"
              ></span>
            </template>
          </el-table-column>

          <el-table-column prop="pieceText" width="220px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-title">{{scope.row.pieceText}}</span>
            </template>
          </el-table-column>

          <!-- <el-table-column prop="pieceCode"></el-table-column> -->

          <el-table-column prop="pieceSize" width="145px">
            <template slot-scope="scope">
              <span
                class="fnsd-chart-table-percentage fnsd-chart-table-separate"
                :style="{'color': `${scope.row.color||'#000'}`}"
              >{{scope.row.pieceSize}}<span>%</span></span>
            </template>
          </el-table-column>

          <el-table-column prop="formattedValue">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-value">{{scope.row.formattedValue}}<span><!--тут ед. измерения--></span></span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </fragment>
</template>

<script>
  import { mapMutations, mapState, mapGetters, mapActions } from 'vuex';
  import Card from '../../components/Card.vue';
  import ClickableChart from './ClickableChart.vue';
  import FilterCode from '../../core/filter-codes';

  export default {
    name: 'Environment',
    components: {
      Card,
      ClickableChart,
    },
    data() {
      return {
        chartMode: true,
      };
    },
    computed: {
      ...mapState({
        period: 'period',
      }),

      ...mapGetters('filter', {
        region: 'region',
        tnved: 'tnved',
        country: 'country',
        eaes: 'eaes',
        taxMode: 'taxMode',
        selectedEnvironment: 'environment',
        serverFilter: 'serverFilter',
      }),

      ...mapGetters('environment', {
        items: 'items',
        series: 'series',
        labels: 'labels',
        colors: 'colors',
      }),

      environmentRadio: {
        get() {
          if (!this.selectedEnvironment) {
            return 'Все';
          }
          return this.selectedEnvironment.pieceText;
        },
        set(value) {
          if (value === 'Все') {
            this.removeFilter(FilterCode.ENVIRONMENT);
          } else {
            this.setEnvironment(this.items.find(e => e.pieceText === value));
          }
        },
      },

      selectedEnvironmentName() {
        return this.selectedEnvironment ? this.selectedEnvironment.pieceText : null;
      },

      tableData() {
        if (this.environmentRadio === 'Все') {
          return this.items;
        }
        return this.items.filter(e => e.pieceText === this.environmentRadio);
      },
    },
    created() {
      this.loadItems(this.serverFilter);
      this.$watch(vm => [vm.period, vm.region, vm.tnved, vm.country, vm.eaes, vm.taxMode].join(), () => this.loadItems(this.serverFilter));
    },
    methods: {
      ...mapMutations('filter', {
        setEnvironment: 'setEnvironment',
        removeFilter: 'removeFilter',
      }),

      ...mapActions('environment', {
        loadItems: 'loadItems',
      }),

      updateEnvironmentSelection(name) {
        const environment = this.items.find(e => e.pieceText === name);
        if (environment) {
          this.setEnvironment(environment);
        }
      },
    },
  };
</script>

