  .radio4 {
    width: 100%;
    user-select: none;
  }

  .radio4 label {
    width: 25%;
  }

  .cell {
    font-weight: 100;
    white-space: pre !important;
  }

  .custom-table table {
    border-spacing: 0 5px;
  }

  .custom-table td {
    border-top: 1px solid #cbdfef;
    border-bottom: 1px solid #cbdfef;
  }

  .custom-table td:first-child {
    border-top-left-radius: 5px;
