<template>
    <g v-if="item && item[indicatorId]"
       class="path"
       :regionId="feature.properties.regionId"
       @mouseenter="mouseenter"
       @mouseleave="mouseleave"
       @click="clicked"
    >
      <path :d="geometry"
            :fill="`rgba(${regionColor}, ${opacity})`"
            :stroke="strokeColor"
            :stroke-width="active ? 1.0 : 0.3"
            class="path"
      ></path>
      <rect  :x="centerX - 20"
             :y="centerY - 10"
             rx="2"
             ry="2"
             width="40"
             height="20"
             fill="#7daed5"
             :opacity="opacity"
             stroke="white"
             stroke-width="1"
      ></rect>
      <text
        :x="centerX"
        :y="centerY + 4"
        text-anchor="middle"
        :font-size="12"
        :opacity="opacity"
        fill="#FFF"
        style="pointer-events: all; cursor: default"
      >{{level===1?feature.properties.foId:feature.properties.regionId}}</text>
    </g>
</template>

<script>
  import { geoPath } from 'd3-geo';
  import { mapState } from 'vuex';
  import { orDefault } from '../../helpers/utils';

  export default {
    name: 'MapRegion',
    props: {
      feature: {
        type: Object,
        required: true,
      },
      projection: {
        type: Function,
        required: true,
      },
      width: {
        type: Number,
        required: false,
        default: 770,
      },
      height: {
        type: Number,
        required: false,
        default: 360,
      },
      activeRegion: {
        type: String,
        required: false,
      },
      item: {
        type: Array,
        required: false,
      },
      isTemp: {
        type: Boolean,
        required: false,
        default: false,
      },
      loading: {
        type: Boolean,
        required: false,
        default: false,
      },
    },

    data() {
      return {
        active: false,
        bbox: {},
        center: {},
        label: {},
      };
    },

    watch: {
      activeRegion(value) {
        if (value === this.feature.properties.foId.toString() || value === this.feature.properties.regionId.toString()) {
          this.active = true;
          this.$emit('on-top', this.$el);
        } else this.active = false;
      },
    },

    mounted() {
      const xmlns = 'http://www.w3.org/2000/svg';
      const svgElem = document.createElementNS(xmlns, 'svg');
      const path = document.createElementNS(xmlns, 'path');
      path.setAttributeNS(null, 'd', this.geometry);
      svgElem.appendChild(path);
      document.getElementById('app').appendChild(svgElem);
      this.bbox = svgElem.getBBox();
      document.getElementById('app').removeChild(svgElem);
      // оставим возможность задавать координаты для регионов у которых автоматически центры посчитаются криво
      if (this.feature.properties.centerX) {
        this.$set(this.center, 'x', this.feature.properties.centerX);
        this.$set(this.center, 'y', this.feature.properties.centerY);
      } else {
        const x = this.bbox.x + (this.bbox.width / 2);
        const y = this.bbox.y + (this.bbox.height / 2);
        this.$set(this.center, 'x', x);
        this.$set(this.center, 'y', y);
      }
    },

    computed: {
      ...mapState({
        indicatorId: state => state.indicatorId,
        level: state => state.level,
      }),

      strokeColor() {
        return this.active ? '#00469B' : '#FFFFFF';
      },

      regionColor() {
        return orDefault(this.item[this.indicatorId].regionColor, '128,163,205');
      },

      centerX() {
        return this.feature.properties.isOutCircle ? this.feature.properties.circleX : this.center.x;
      },

      centerY() {
        return this.feature.properties.isOutCircle ? this.feature.properties.circleY : this.center.y;
      },

      geometry() {
        const p = this.projection;
        return geoPath()
          .projection(p)(this.feature);
      },

      opacity() {
        if (this.active) {
          return 1;
        }
        return this.activeRegion ? 0.3 : 1;
      },
    },

    methods: {
      clicked() {
        if (!this.isTemp) {
          this.$emit('click', this.featureInfo());
        }
      },

      featureInfo() {
        return {
          regionId: this.feature.properties.regionId,
          name: this.feature.properties.name,
          foId: this.feature.properties.foId,
        };
      },

      mouseenter() {
        if (this.isTemp) {
          return;
        }
        this.active = true;
        this.$emit('onmouseenter', this.$el, this.level === 1 ? this.feature.properties.foId : this.feature.properties.regionId);
      },

      mouseleave() {
        if (this.isTemp) {
          return;
        }
        this.active = false;
        // const paths = document.querySelectorAll('g[class="path"');
        // paths.forEach((path) => {
        //   path.style.opacity = null;
        // });
        this.$emit('onmouseleave');
      },

      // mousemove(event) {
      //    if (!this.feature.properties.textX) {
      //     // this.$emit('onmousemove', event.clientX - 10, event.clientY - 170);
      //    }
      // },

    },
  };
</script>
