import Vue from 'vue';
import Vuex from 'vuex';

import geometry from './modules/geometry';
import { indicatorsModule } from './modules/indicators';
import { diagramsModule } from './modules/diagrams';
import { scaleModule } from './modules/scale';
import { filterModule } from './modules/filter';
import { tnvedModule } from './modules/data/tnved';
import { createTop5Module } from './modules/data/top5-module';
import { createSimpleDataModule } from './modules/data/simple-data-module';
import pieChartService from '../services/pie-chart-service';

Vue.use(Vuex);

const store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  modules: {
    geometry,
    indicators: indicatorsModule,
    diagrams: diagramsModule,
    scale: scaleModule,
    filter: filterModule,
    tnved: tnvedModule,
    country: createTop5Module(pieChartService, 'getCountryCharts'),
    environment: createSimpleDataModule(pieChartService, 'getEnvironmentCharts'),
    eaes: createSimpleDataModule(pieChartService, 'getEaesCharts'),
    dropoutReasons: createTop5Module(pieChartService, 'getDropoutReasons'),
    taxModes: createSimpleDataModule(pieChartService, 'getTaxModes'),
    violations: createTop5Module(pieChartService, 'getViolations'),
    risks: createTop5Module(pieChartService, 'getRisks'),
    conflicts: createTop5Module(pieChartService, 'getConflicts'),
    penalties: createTop5Module(pieChartService, 'getPenalties'),
    conflictsResolved: createSimpleDataModule(pieChartService, 'getResolvedConflicts'),
    rnpt: createSimpleDataModule(pieChartService, 'getRnptCount'),
  },
  state: {
    indicatorId: 0,
    level: 0,
    period: null,
    selectedRegion: null,
    selectedTnved: null,
    selectedCountry: null,
    selectedEnvironment: null,
    legend: [],
    dim: {},
  },
  mutations: {
    SET_DIM: (state, payload) => {
      state.dim = payload;
    },
    SET_LEGEND: (state, payload) => {
      state.legend = payload;
    },
    EXPAND_GROUP: (state, payload) => {
      state.legend[payload].expand = !state.legend[payload].expand;
    },
    SET_REGION: (state, payload) => {
      state.selectedRegion = payload;
    },
    SET_TNVED: (state, payload) => {
      state.selectedTnved = payload;
    },
    SET_COUNTRY: (state, payload) => {
      state.selectedCountry = payload;
    },
    SET_ENVIRONMENT: (state, payload) => {
      state.selectedEnvironment = payload;
    },
    SET_INDICATOR_ID: (state, payload) => {
      state.indicatorId = payload;
    },
    SET_LEVEL: (state, payload) => {
      state.level = payload;
    },
    SET_PERIOD: (state, payload) => {
      state.period = payload;
    },
  },
});

export default store;
