<template>
  <div>
    <div class="fnsd-indicator-menu"
         :style="{ 'left': menuVisible ? '0' : '-550px' }"
         @mouseleave="hideMenu"
         @mouseenter="showMenu"
    >
      <div class="fnsd-indicator-menu-inner">
        <div class="fnsd-indicator-menu-title">
          Показатели:
        </div>
        <div
          class="fnsd-indicator-menu-group"
          v-for="group in indicatorGroups"
          :key="group.code"
          @click.self="toggleGroup(group.code)"
        >
          <div @click="toggleGroup(group.code)" class="fnsd-indicator-menu-group-title">
            <span v-html="orDefault(group.name, HTML_ENTITY.EMPTY)"></span>
          </div>
          <transition-group name="fade">
            <div v-if="group.expanded" key="expanded">
              <indicator-menu-item :indicators="group.indicators" @set:indicator="setCurrentIndicator($event)"></indicator-menu-item>
            </div>
          </transition-group>
        </div>
      </div>
    </div>
    <div class="fnsd-map-selector-control"
         :style="{ 'left': menuVisible ? '-100px' : '0px' }"
         style="transition: 0.8s linear"
    >
      <div class="fnsd-map-selector-control-trapeze" @mouseenter="showMenu">
        <span class="tab">Показатели</span>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapMutations, mapActions } from 'vuex';
  import HtmlEntity from '../helpers/html-entity';
  import IndicatorMenuItem from './IndicatorMenuItem.vue';

  export default {
    name: 'IndicatorMenu',
    components: { IndicatorMenuItem },
    data() {
      return {
        HTML_ENTITY: HtmlEntity,
        menuVisible: false,
      };
    },
    computed: {
      ...mapState('indicators', {
        indicatorGroups: 'indicatorGroups',
        currentIndicator: 'currentIndicator',
      }),
    },
    methods: {
      showMenu() {
        this.menuVisible = true;
      },

      hideMenu() {
        this.menuVisible = false;
      },

      ...mapMutations('indicators', {
        toggleGroup: 'toggleGroup',
      }),

      ...mapActions('indicators', {
        setCurrentIndicator: 'setCurrentIndicator',
      }),

      onIndicatorSet(indicator) {
        console.log(indicator);
      },
    },
  };
</script>
