<template>
  <el-container>
    <indicator-menu></indicator-menu>
    <el-header class="fnsd-page-header">
      <el-row :gutter="30">
        <el-col :span="12">
          <div class="fnsd-page-header-title">{{currentIndicatorGroup.name}}</div>
          <div class="fnsd-page-header-subtitle">{{currentIndicator.name}}</div>
        </el-col>

        <el-col :span="12">
          <div class="fnsd-page-header-param">
            <filter-panel></filter-panel>
            <scale-panel></scale-panel>
          </div>
        </el-col>
      </el-row>
    </el-header>

    <el-main class="fnsd-page-content">
      <el-row :gutter="30">
        <el-col :span="12">
          <el-card ref="map" class="fnds-map-panel">
            <MapPage v-bind="mapProps"
                     @map-mode-changed="mapModeChanged"
            ></MapPage>
          </el-card>
        </el-col>

        <el-col :span="12" :class="['fnsd-cards-count-' + visibleDiagrams.length]">
          <collapse-impl v-model="activeFilter" class="tabs" accordion>
            <el-collapse-item
              v-for="(diagram, index) in visibleDiagrams"
              :key="diagram.key"
              :name="(index + 1).toString()"
              class="el-card is-always-shadow"
            >
              <template slot="title">
                <div class="fnsd-card-title">{{diagram.title}}</div>
              </template>
              <div class="fnsd-card-item">
                <component :is="diagram.component"></component>
              </div>
            </el-collapse-item>
          </collapse-impl>
        </el-col>
      </el-row>
      <el-row ref="tableData" :gutter="20" style="padding-top:20px">
        <el-col :span="24">
          <component :is="tableComponent"
                     v-bind="tableProps"
                     @map-mode-changed="mapModeChanged"
          ></component>
        </el-col>
      </el-row>
    </el-main>

    <el-footer class="fnsd-page-footer">
      <Periods></Periods>
    </el-footer>
  </el-container>
</template>

<script>
  import { Collapse } from 'element-ui';
  import { mapMutations, mapState, mapGetters } from 'vuex';
  import Tooltip from '../components/map/Tooltip.vue';
  import IndicatorMenu from '../components/IndicatorMenu.vue';
  import ScalePanel from '../components/ScalePanel.vue';
  import FilterPanel from '../components/FilterPanel.vue';
  import Tnved from './chart/Tnved.vue';
  import { getLegendByIndex } from '../helpers/utils';
  import MapMode from './map/map-mode';
  import Periods from '../components/Periods.vue';
  import TaxPage from './table/TaxPage.vue';
  import Country from './chart/Country.vue';
  import MapPage from './map/MapPage.vue';
  import Environment from './chart/Environment.vue';
  import TaxpayerPage from './table/TaxpayerPage.vue';
  import Eaes from './chart/Eaes.vue';
  import DropoutReasons from './chart/DropoutReasons.vue';
  import TaxModes from './chart/TaxModes.vue';
  import Violations from './chart/Violations.vue';
  import Risks from './chart/Risks.vue';
  import Conflicts from './chart/Conflicts.vue';
  import Penalties from './chart/Penalties.vue';
  import ConflictsResolved from './chart/ConflictsResolved.vue';
  import RnptCount from './chart/RnptCount.vue';
  import FilterCode from '../core/filter-codes';

  const CollapseImpl = {
    name: 'CollapseImpl',
    extends: Collapse,
    methods: {
      handleItemClick(item) {
        if (this.accordion) {
          this.setActiveNames(item.name);
        }
      },
    },
  };

  export default {
    name: 'Dashboard',
    components: {
      Environment,
      MapPage,
      IndicatorMenu,
      ScalePanel,
      FilterPanel,
      Country,
      Periods,
      Tnved,
      CollapseImpl,
      Tooltip,
      Eaes,
      DropoutReasons,
      TaxModes,
      Violations,
      Risks,
      Conflicts,
      Penalties,
      ConflictsResolved,
      RnptCount,
    },
    data() {
      return {
        activeFilter: '1',
        periods: [],
        tableData: [],
        tableComponent: TaxPage,
        tableProps: {},
        mapProps: {},
        mapHeight: 0,
      };
    },
    computed: {
      ...mapState({
        legend: state => state.legend,
        indicatorId: state => state.indicatorId,
      }),
      ...mapState('indicators', {
        currentIndicator: 'currentIndicator',
      }),
      ...mapGetters('indicators', {
        currentIndicatorGroup: 'currentIndicatorGroup',
      }),
      ...mapGetters('diagrams', {
        visibleDiagrams: 'visibleDiagrams',
      }),
      ...mapGetters('filter', {
        region: 'region',
      }),

      currentLegend() {
        return getLegendByIndex(this.legend, this.indicatorId);
      },
    },
    watch: {
      currentIndicator() {
        this.activeFilter = '1';
      },
      region(region) {
        if (!region) {
          this.mapModeChanged({ props: { mode: MapMode.RUSSIA } });
        }
      },
    },
    created() {
      this.setLevel(1);
    },
    beforeDestroy() {
      window.removeEventListener('resize', this.getMapHeight);
    },
    mounted() {
      this.$nextTick(() => {
        window.addEventListener('resize', this.getMapHeight);
        this.getMapHeight();
      });
    },
    methods: {
      ...mapMutations({
        setLevel: 'SET_LEVEL',
      }),
      ...mapMutations('filter', {
        setRegion: 'setRegion',
        removeFilter: 'removeFilter',
      }),

      getMapHeight() {
        this.mapHeight = this.$refs.map.$el.clientHeight;
      },

      mapModeChanged(info) {
        console.log(info);
        switch (info.mode) {
          case MapMode.FEDERAL_DISTRICT:
            this.setLevel(2);
            this.setRegion(info.props.federalDistrict);
            this.mapProps = info.props;
            break;
          case MapMode.REGION:
            this.tableComponent = TaxPage;
            this.$refs.tableData.$el.scrollIntoView();
            break;
          case MapMode.TAX_PAGE:
            this.tableComponent = TaxpayerPage;
            this.tableProps = info.props;
            break;
          default:
            this.setLevel(1);
            this.removeFilter(FilterCode.REGION);
        }
        this.$nextTick(() => this.getMapHeight());
      },
    },
  };
</script>

<style>
  .tabs .el-icon-arrow-right {
    display: none;
  }

  .bg-purple {
    background: #d3dce6;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 360px;
  }
</style>
