import { geoAlbers } from 'd3-geo';

export default {
  DEFAULT_WIDTH: 770,
  DEFAULT_HEIGHT: 360,

  centeredFeature(feature, width, height) {
    const w = width || this.DEFAULT_WIDTH;
    const h = height || this.DEFAULT_HEIGHT;

    return geoAlbers()
      .rotate([-100, 0])
      .center([1, 64])
      .parallels([52, 64])
      .precision(0.1)
      .fitSize([w, h], feature);
  },
};
