import BaseService from './base-service';

class FederalDistrictInfoService extends BaseService {

  getFederalDistrictsInfoLevel(rowKeyValues, taxPeriod, countryCode, isTemporary, taxType, violationCode, riskCode) {
    const params = {
      isTemporary,
      taxPeriod,
      countryCode,
      taxType,
      rowKeyValues,
      violationCode,
      riskCode,
    };
    return this.$http.post('/GetDistrictIndicators', params);
  }
}

const federalDistrictInfoService = new FederalDistrictInfoService();

export default federalDistrictInfoService;
