import IndicatorGroupCode from '../../../core/indicator-group-code';
import IndicatorCode from '../../../core/indicator-codes';

const indicatorState = {
  indicatorGroups: [
    {
      name: 'Макроэкономические показатели',
      code: IndicatorGroupCode.MACROECONOMIC_INDICATORS,
      expanded: true,
      indicators: [
        { name: 'Ввезено', code: IndicatorCode.D_IMPORT, children: [] },
        { name: 'Экспорт', code: IndicatorCode.D_EXPORT, children: [] },
        {
          name: 'Оборот',
          children: [
            { name: 'Выбытие товаров', code: IndicatorCode.D_EXIT, children: [] },
            { name: 'Зарегистрированные остатки', code: IndicatorCode.REGISTERED_BALANCE, children: [] },
            { name: 'Стоимость товаров, которые находятся в обороте', code: IndicatorCode.CIRCULATION, children: [] },
          ],
        },
      ],
    },
    {
      name: 'Участники оборота',
      code: IndicatorGroupCode.TURNOVER_PARTICIPANTS,
      expanded: false,
      indicators: [
        { name: 'Импортеры', code: IndicatorCode.IMPORTERS, children: [] },
        { name: 'Экспортеры', code: IndicatorCode.EXPORTERS, children: [] },
        { name: 'Внутренний оборот', code: IndicatorCode.DISTRIBUTION, children: [] },
        { name: 'Потребители товаров и розничные продавцы', code: IndicatorCode.RETAIL, children: [] },
      ],
    },
    {
      name: 'Контрольная работа',
      code: IndicatorGroupCode.CONTROL_WORK,
      expanded: false,
      indicators: [
        { name: 'Расхождения', code: IndicatorCode.COUNT_VIOLATIONS, children: [] },
        { name: 'Риски', code: IndicatorCode.COUNT_RISKS, children: [] },
        { name: 'Истребования', code: IndicatorCode.D_DOC_REQUEST, children: [] },
        { name: 'Противоречия', code: IndicatorCode.CONFLICT, children: [] },
        { name: 'Штрафы', code: IndicatorCode.PENALTY, children: [] },
        { name: 'Уведомления', code: IndicatorCode.NOTIFY, children: [] },
      ],
    },
    {
      name: 'Технологическое направление',
      code: IndicatorGroupCode.TECHNOLOGICAL_DIRECTION,
      expanded: false,
      indicators: [
        { name: 'Обработано уведомлений об остатках', code: IndicatorCode.NOTICE_REG_B, children: [] },
        { name: 'Обработано уведомлений о ввозе', code: IndicatorCode.NOTICE_REG_I, children: [] },
      ],
    },
  ],
  currentIndicator: { name: 'Ввезено', code: IndicatorCode.D_IMPORT, children: [] },
};

const indicatorGetters = {
  currentIndicatorGroup(state) {
    return state.indicatorGroups.find((group) => {
      const indicators = [];
      group.indicators.forEach((indicator) => {
        indicators.push(indicator);
        indicators.push(indicator.children);
      });
      const indicator = indicators.flat().find(i => i.code === state.currentIndicator.code);
      return !!indicator;
    });
  },
};

const indicatorMutations = {
  setCurrentIndicator(state, indicator) {
    state.currentIndicator = indicator;
  },

  toggleGroup(state, code) {
    const group = state.indicatorGroups.find(g => g.code === code);
    group.expanded = !group.expanded;
  },
};

const indicatorActions = {
  setCurrentIndicator({ commit, rootState, dispatch }, indicator) {
    dispatch('filter/clearFilters', null, { root: true });
    commit('setCurrentIndicator', indicator);

    // Костыль, предназначенный для обспечения совместимости со старым кодом
    // TODO убрать данный костыль
    let result = null;
    rootState.legend.forEach((group) => {
      const found = group.data.find(i => i.code === indicator.code);
      if (found) {
        result = found;
      }
    });
    if (result) {
      commit('SET_INDICATOR_ID', result.index, { root: true });
    }
  },
};

export const indicatorsModule = {
  namespaced: true,
  state: indicatorState,
  getters: indicatorGetters,
  mutations: indicatorMutations,
  actions: indicatorActions,
};
