<template>
  <svg version="1.1"
       baseProfile="full"
       xmlns="http://www.w3.org/2000/svg"
       :viewBox="'0 0 ' + width + ' ' + height">
    <g ref="map">
      <map-region v-for="feature in features"
                  :key="feature.properties.regionId"
                  :item="items[level === 1 ? feature.properties.foId : feature.properties.regionId]"
                  :feature="feature"
                  :projection="projection"
                  :is-temp="isTemp"
                  :loading="loading"
                  :active-region="activeRegion"
                  @click="$emit('click', $event)"
                  @onmouseenter="onMouseEnter"
                  @onmouseleave="onMouseLeave"
                  @on-top="onTop"
                  ref="regions"
      ></map-region>
    </g>
  </svg>
</template>

<script>
import { mapState } from 'vuex';
import MapRegion from './MapRegion.vue';

export default {
  name: 'Map',
  components: {
    MapRegion,
  },
  props: {
    width: {
      type: Number,
      required: false,
      default: 760,
    },
    height: {
      type: Number,
      required: false,
      default: 360,
    },
    features: {
      type: Array,
      required: true,
    },
    items: {
      type: Object,
      required: true,
    },
    activeRegion: {
      type: String,
      required: false,
    },
    projection: {
      type: Function,
      required: true,
    },
    isTemp: {
      type: Boolean,
      required: false,
      default: false,
    },
    loading: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  data() {
    return {
      labels: [],
    };
  },
  computed: {
    ...mapState({
      level: state => state.level,
    }),
  },

  methods: {
    onMouseEnter(el, regionId) {
      this.$emit('onmouseenter', regionId.toString());
      this.onTop(el);
    },
    onTop(el) {
      this.$refs.map.appendChild(el);
    },
    onMouseLeave() {
      this.$emit('onmouseenter', '');
      this.labels.forEach((r) => {
        if (!r.freeze) r.visibility = 'hidden';
      });
    },
  },
};
</script>
