import BaseService from './base-service';

class TaxPeriodsService extends BaseService {
  getTaxPeriods() {
    return this.$http.get('/GetTaxPeriods');
  }

  getPeriodsByCode(indicatorCode) {
    const params = {
      indicatorCode,
    };
    return this.$http.post('/GetPeriods', params).then(response => response.data);
  }
}

const taxPeriodsService = new TaxPeriodsService();

export default taxPeriodsService;
