<template>
  <el-card class="fnsd-tax-card">
    <div class="fnsd-card-title">ПЕРЕЧЕНЬ НО</div>
    <el-input
      placeholder="Поиск"
      class="fnsd-tax-search"
      prefix-icon="el-icon-search"
      v-model="filter">
      <i slot="suffix" @click="filter=''" title="Очистить" class="el-input__icon el-icon-close" style="cursor: pointer"></i>
    </el-input>
    <el-table
      :data="tableData"
      height="250"
      style="width: 100%"
      @row-click="onRowClick"
    >
      <el-table-column
        prop="TAX_CODE"
        label="Код НО"
      ></el-table-column>
      <el-table-column
        prop="TAX_CODE_NAME"
        label="Наименование налогового органа"
      ></el-table-column>
      <el-table-column
        prop="formattedValue"
        :label="currentLegend.text"
      ></el-table-column>
      <el-table-column
        prop="TP_COUNT"
        label="Количество НП"
      ></el-table-column>
      <el-table-column
        prop="RNPT_COUNT"
        label="Количество партий"
      ></el-table-column>
    </el-table>
  </el-card>
</template>

<script>
  import { mapState, mapGetters } from 'vuex';
  import regionInfoService from '../../services/region-info-service';

  import { getLegendByIndex } from '../../helpers/utils';
  import MapMode from '../map/map-mode';

  export default {
    name: 'TaxPage',
    data() {
      return {
        valueColumnName: '',
        filter: '',
        isError: false,
        tableData: [],
        columns: {},
        innerColumns: [],
        isEmpty: false,
        visible: false,
      };
    },
    computed: {
      ...mapState({
        legend: state => state.legend,
        currentPeriod: state => state.period,
        indicatorId: state => state.indicatorId,
      }),
      ...mapState('scale', {
        currentScale: 'currentScale',
      }),
      ...mapGetters('scale', {
        scaledValue: 'scaledValue',
      }),
      ...mapGetters('filter', {
        currentRegion: 'region',
      }),
      currentLegend() {
        return getLegendByIndex(this.legend, this.indicatorId);
      },
    },

    watch: {
      currentScale() {
        this.updateFormattedValues();
      },
    },

    created() {
      this.updateTable();
    },

    methods: {
      onRowClick(a) {
        this.$emit('map-mode-changed', {
          mode: MapMode.TAX_PAGE,
          props: {
            taxCode: a.TAX_CODE,
          },
        });
      },
      updateTable() {
        this.isEmpty = false;
        const countryCode = '';
        this.columns = {};
        regionInfoService.getRegionTableData(
          this.currentRegion ? [this.currentRegion.regionId] : null,
          this.currentPeriod,
          countryCode,
          this.indicator ? this.indicator.code : '',
          this.taxType ? this.taxType.code : '',
          this.violationFilter ? this.violationFilter.Code : '',
          this.riskFilter ? this.riskFilter.Code : '',
        ).then((response) => {
          console.log(response);
          this.tableData = response.data.data;
          if (this.tableData.length) {
            this.valueColumnName = `${Object.keys(this.tableData[0])[2]}`;
          }
          this.updateFormattedValues();
        })
          .catch((error) => {
            console.log(error.response || error);
            this.isError = true;
          });
      },

      updateFormattedValues() {
        this.tableData.forEach((row) => {
          const col = row[this.valueColumnName];
          row.formattedValue = this.scaledValue(col);
        });
        // todo не обновляется значение в таблице без манипуляций
        this.tableData.push({});
        this.tableData.pop();
      },

    },
  };
</script>
