<template>
  <fragment>
    <div v-if="chartMode">
      <el-row>
        <el-col :span="12">
          <div class="fnsd-chart-centred">
            <clickable-chart
              label-top="ТОП-5"
              label-bottom="РАСХОЖДЕНИЯ"
              :series="series"
              :labels="labels"
              :colors="colors"
              :selected="selectedViolationName"
              @slice:select="updateViolationSelection"
            ></clickable-chart>
          </div>
        </el-col>

        <el-col :span="12">
          <div class="fnsd-chart-mode">
            <img src="../../images/button-chart-pressed.png">
            <img @click="chartMode = false" src="../../images/button-list.png" class="img-active">
          </div>
          <div class="fnsd-chart-legend-column fnsd-chart-legend-long-label">
            <div class="fnsd-chart-legend-item"
                 v-for="(violation, index) in legend" :key="index"
            >
              <div class="fnsd-chart-legend-item-inner"
                   :style=
                     "`opacity: ${selectedViolation ? (selectedViolation.pieceText === selectedViolation.pieceText ? 1 : 0.3) : 1}; cursor: pointer;`"
                   @click="setViolation(violation)"
              >
                <card direction="horizontalAlt"
                      :title="violation.pieceText"
                      :color="violation.color"
                      :percentage="violation.pieceSize"
                      :value="violation.formattedValue"
                ></card>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <div v-else class="fnsd-chart-panel fnsd-chart-country">
      <div class="fnsd-chart-mode">
        <img @click="chartMode = true" src="../../images/button-chart.png" class="img-active">
        <img src="../../images/button-list-pressed.png">
      </div>

      <div class="fnsd-chart-radio fnsd-chart-radio-compact">
        <el-radio-group v-model="radio" class="fnsd-chart-radio-count-3">
          <el-radio-button label="all">Все</el-radio-button>
          <el-radio-button label="top5">ТОП-5</el-radio-button>
          <el-radio-button label="other">Прочие</el-radio-button>
        </el-radio-group>
      </div>

      <div class="fnsd-list-search">
        <el-input
          prefix-icon="el-icon-search"
          v-model="filter">
          <i slot="suffix" @click="filter=''" title="Очистить" class="el-input__icon el-icon-close" style="cursor: pointer"></i>
        </el-input>
      </div>

      <div class="fnsd-chart-table fnsd-chart-table-compact">
        <el-table
          :data="tableData"
          height="340"
          class="custom-table"
          style="width: 100%;"
        >
          <el-table-column width="5px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-point"
                    :style="{'background-color': `${scope.row.color||'rgba(0, 0, 0, 0.1'}`}"
              ></span>
            </template>
          </el-table-column>

          <el-table-column
            label="Наименование"
            sortable
            prop="pieceText"
          >
            <template slot-scope="scope">
              <span class="fnsd-table-country-td-title">{{scope.row.pieceText}}</span>
            </template>
          </el-table-column>

          <el-table-column
            label="Код"
            sortable
            width="70px"
            prop="pieceCode"
          >
            <template slot-scope="scope">
              <span class="fnsd-table-country-td-code">{{scope.row.pieceCode}}</span>
            </template>
          </el-table-column>

          <el-table-column
            label="Значение в %"
            sortable
            width="133px"
            prop="pieceSize"
          ></el-table-column>

          <el-table-column
            label="Значение в ₽"
            sortable
            width="200px"
            :sort-method="sortValues"
            prop="formattedValue"
          ></el-table-column>
        </el-table>
      </div>
    </div>
  </fragment>
</template>

<script>
  import { mapState, mapMutations, mapGetters, mapActions } from 'vuex';
  import Card from '../../components/Card.vue';
  import ClickableChart from './ClickableChart.vue';

  export default {
    name: 'Violations',
    components: {
      Card,
      ClickableChart,
    },
    data() {
      return {
        radio: 'all',
        filter: '',
        chartMode: true,
      };
    },
    computed: {
      ...mapState({
        period: 'period',
      }),

      ...mapGetters('filter', {
        region: 'region',
        tnved: 'tnved',
        selectedViolation: 'violation',
        serverFilter: 'serverFilter',
      }),

      ...mapGetters('violations', {
        legend: 'legend',
        series: 'series',
        labels: 'labels',
        colors: 'colors',
        sortedItems: 'sortedItems',
        top5: 'top5',
        others: 'others',
      }),

      selectedViolationName() {
        return this.selectedViolation ? this.selectedViolation.pieceText : null;
      },

      tableData() {
        const filterStr = this.filter.toLowerCase();
        const predicate = e => e.pieceText.toLowerCase().includes(filterStr) || e.pieceCode.toLowerCase().includes(filterStr);
        switch (this.radio) {
          case 'all':
            return this.sortedItems.filter(e => predicate(e));
          case 'top5':
            return this.top5.filter(e => predicate(e));
          case 'other':
            return this.others.filter(e => predicate(e));
          default:
            return [];
        }
      },
    },
    created() {
      this.loadItems(this.serverFilter);
      this.$watch(vm => [vm.period, vm.region, vm.tnved].join(), () => this.loadItems(this.serverFilter));
    },
    methods: {
      ...mapMutations('filter', {
        setViolation: 'setViolation',
      }),

      ...mapActions('violations', {
        loadItems: 'loadItems',
      }),

      updateViolationSelection(name) {
        const risk = this.legend.find(r => r.pieceText === name);
        if (risk) {
          this.setViolation(risk);
        }
      },

      sortValues(a, b) {
        return a.pieceValue - b.pieceValue;
      },
    },
  };
</script>

