<template>
  <vue-apex-charts ref="chart"
                   type="donut"
                   :height="height"
                   :options="chartOptions"
                   :series="series"
  ></vue-apex-charts>
</template>

<script>
  import VueApexCharts from 'vue-apexcharts';
  import createChartOptions from './options-factory';
  import { createLabels } from '../../helpers/utils';

  export default {
    name: 'ClickableChart',
    components: { VueApexCharts },
    props: {
      labels: {
        type: Array,
        required: true,
      },
      colors: {
        type: Array,
        required: true,
      },
      series: {
        type: Array,
        required: true,
      },
      labelTop: {
        type: String,
        required: false,
        default: '',
      },
      labelBottom: {
        type: String,
        required: false,
        default: '',
      },
      selected: {
        type: String,
        required: false,
        default: null,
      },
      height: {
        type: String,
        required: false,
        default: 'auto',
      },
    },
    data() {
      return {
        options: createChartOptions('donut', {
          labels: [],
          colors: [],
          chart: {
            id: 'pie-chart-id',
            events: {
              updated: () => {
                this.createLabels();
                this.updateSliceSelection(this.selectedTnved);
              },
              dataPointSelection: (event, chartContext, config) => {
                this.$emit('slice:select', this.labels[config.dataPointIndex]);
              },
            },
          },
        }),
      };
    },
    computed: {
      chartOptions() {
        return { ...this.options, labels: this.labels, colors: this.colors };
      },
    },
    watch: {
      selected() {
        this.updateSliceSelection();
      },
    },
    methods: {
      createLabels() {
        setTimeout(() => {
          const svgElem = this.$refs.chart.$el.querySelector('svg');
          createLabels(svgElem, this.labelTop, this.labelBottom);
        });
      },
      updateSliceSelection() {
        if (this.selected) {
          this.selectSlice(this.selected);
        } else {
          this.deselectSlices();
        }
      },
      selectSlice(name) {
        const index = this.labels.findIndex(label => label === name);
        if (index === -1) {
          return;
        }
        const slices = this.$refs.chart.$el.querySelectorAll('.apexcharts-series');
        slices.forEach((slice, key) => {
          if (key !== index) {
            slice.classList.add('fnsd-chart-slice-inactive');
          } else {
            slice.classList.remove('fnsd-chart-slice-inactive');
          }
        });
      },

      deselectSlices() {
        const slices = this.$refs.chart.$el.querySelectorAll('.apexcharts-series');
        slices.forEach(slice => slice.classList.remove('fnsd-chart-slice-inactive'));
      },
    },
  };
</script>

