import pieChartService from '../../../../services/pie-chart-service';
import rgbToHex from '../../../../helpers/rgb-helpers';

const tnvedState = {
  tnveds: [],
};

const tnvedGetters = {
  flatTnveds(state, getters, rootState, rootGetters) {
    const result = [];
    state.tnveds.forEach((tnved) => {
      const group = tnved.groupName;
      tnved.tnveds.forEach((t) => {
        const { piece } = t;
        result.push({
          ...piece,
          group,
          color: rgbToHex(piece.pieceColor),
          formattedValue: rootGetters['scale/scaledValue'](piece.pieceValue),
          rows: t.rows,
        });
      });
    });
    return result.sort((a, b) => b.pieceValue - a.pieceValue);
  },

  top5(state, getters) {
    return getters.flatTnveds.slice(0, 5);
  },

  others(state, getters) {
    return getters.flatTnveds.slice(5);
  },

  othersValues(state, getters) {
    const { others } = getters;
    if (others.length > 0) {
      return {
        pieceSize: others.reduce((a, { pieceSize }) => a + pieceSize, 0),
        pieceValue: others.reduce((a, { pieceValue }) => a + pieceValue, 0),
        codes: others.map(piece => piece.pieceCode),
      };
    }
    return null;
  },

  legend(state, getters, rootState, rootGetters) {
    const result = [];
    getters.top5.forEach((tnved) => {
      const found = result.find(p => p.name === tnved.group);
      if (found) {
        found.pieces.push({ ...tnved, codes: [tnved.pieceCode] });
      } else {
        const group = {
          name: tnved.group,
          pieces: [{ ...tnved, codes: [tnved.pieceCode] }],
        };
        result.push(group);
      }
    });
    const { othersValues } = getters;
    if (othersValues) {
      result.push({
        name: 'Прочие',
        pieces: [{
          color: rgbToHex('255,201,89'),
          pieceColor: '255,201,89',
          pieceSize: othersValues.pieceSize,
          pieceValue: othersValues.pieceValue,
          pieceText: 'Прочие',
          codes: getters.othersValues.codes,
          formattedValue: rootGetters['scale/scaledValue'](othersValues.pieceValue),
        }],
      });
    }
    return result;
  },

  tnvedSeries(state, getters) {
    const result = getters.top5.map(piece => piece.pieceSize);
    if (getters.othersValues) {
      result.push(getters.othersValues.pieceSize);
    }
    return result;
  },

  tnvedLabels(state, getters) {
    const result = getters.top5.map(piece => piece.pieceText);
    if (getters.othersValues) {
      result.push('Прочие');
    }
    return result;
  },

  tnvedColors(state, getters) {
    const result = getters.top5.map(piece => piece.color);
    if (getters.othersValues) {
      result.push(rgbToHex('255,201,89'));
    }
    return result;
  },
};

const tnvedMutations = {
  setTnveds(state, tnveds) {
    state.tnveds = tnveds;
  },
};

const tnvedActions = {

  async loadTnveds({ commit }, filter) {
    const tnvedData = await pieChartService.getTnvedCharts(filter);
    commit('setTnveds', tnvedData.mapLegend);
  },
};

export const tnvedModule = {
  namespaced: true,
  state: tnvedState,
  getters: tnvedGetters,
  mutations: tnvedMutations,
  actions: tnvedActions,
};
