import FilterCode from '../../../core/filter-codes';

const filterState = {
  [FilterCode.REGION]: null,
  [FilterCode.TNVED]: null,
  [FilterCode.COUNTRY]: null,
  [FilterCode.ENVIRONMENT]: null,
  [FilterCode.EAES]: null,
  [FilterCode.DROPOUT_REASONS]: null,
  [FilterCode.TAX_MODES]: null,
  [FilterCode.VIOLATIONS]: null,
  [FilterCode.RISKS]: null,
  [FilterCode.CONFLICTS]: null,
  [FilterCode.PENALTIES]: null,
  [FilterCode.CONFLICTS_RESOLVED]: null,
  [FilterCode.RNPT_COUNT]: null,
};

const filterGetters = {
  region(state) {
    const region = state[FilterCode.REGION];
    return region ? region.value : null;
  },
  tnved(state, getters, rootState, rootGetters) {
    const tnved = state[FilterCode.TNVED];
    return tnved ? { ...(tnved.value), formattedValue: rootGetters['scale/scaledValue'](tnved.value.pieceValue) } : null;
  },
  country(state) {
    const country = state[FilterCode.COUNTRY];
    return country ? country.value : null;
  },
  environment(state) {
    const environment = state[FilterCode.ENVIRONMENT];
    return environment ? environment.value : null;
  },
  eaes(state) {
    const eaes = state[FilterCode.EAES];
    return eaes ? eaes.value : null;
  },
  dropoutReason(state) {
    const reason = state[FilterCode.DROPOUT_REASONS];
    return reason ? reason.value : null;
  },
  taxMode(state) {
    const mode = state[FilterCode.TAX_MODES];
    return mode ? mode.value : null;
  },
  violation(state) {
    const violation = state[FilterCode.VIOLATIONS];
    return violation ? violation.value : null;
  },
  risk(state) {
    const risk = state[FilterCode.RISKS];
    return risk ? risk.value : null;
  },
  conflict(state) {
    const conflict = state[FilterCode.RISKS];
    return conflict ? conflict.value : null;
  },
  penalty(state) {
    const penalty = state[FilterCode.PENALTIES];
    return penalty ? penalty.value : null;
  },
  conflictsResolved(state) {
    const conflictsResolved = state[FilterCode.CONFLICTS_RESOLVED];
    return conflictsResolved ? conflictsResolved.value : null;
  },
  rnpt(state) {
    const rnpt = state[FilterCode.RNPT_COUNT];
    return rnpt ? rnpt.value : null;
  },

  currentFilters(state) {
    return Object.keys(state).filter(key => !!state[key]).map(key => state[key]);
  },

  serverFilter(state, getters, rootState, rootGetters) {
    const { period } = rootState;
    const region = state[FilterCode.REGION];
    const regionCodes = region ? rootGetters['geometry/regionByDistrictId'](region.value.foId).map(r => r.properties.regionId) : null;
    const indicator = rootState.indicators.currentIndicator;
    const tnved = state[FilterCode.TNVED];
    const country = state[FilterCode.COUNTRY];
    let countryCodes = null;
    if (country) {
      countryCodes = country.value.codes;
    } else {
      const eaes = state[FilterCode.EAES];
      if (eaes) {
        countryCodes = [eaes.value.pieceCode];
      }
    }
    const environment = state[FilterCode.ENVIRONMENT];
    // const dropoutReasons = state[FilterCode.DROPOUT_REASONS];
    const taxMode = state[FilterCode.TAX_MODES];
    const violation = state[FilterCode.VIOLATIONS];
    const risk = state[FilterCode.RISKS];
    // const conflict = state[FilterCode.CONFLICTS];
    // const penalty = state[FilterCode.PENALTIES];
    // const conflictsResolved = state[FilterCode.CONFLICTS_RESOLVED];
    const rnpt = state[FilterCode.RNPT_COUNT];
    return {
      RowKeyValues: regionCodes,
      Level: rootState.level,
      IndicatorCode: indicator ? indicator.code : null,
      TaxPeriod: period,
      TnvedCode: tnved ? tnved.value.codes : null,
      CountryCode: countryCodes,
      EnvironmentCode: environment ? environment.value.pieceCode : null,
      TaxType: taxMode ? taxMode.value.pieceCode : null,
      ViolationCode: violation ? violation.value.codes : null,
      RiskCode: risk ? risk.value.codes : null,
      Rnpt: rnpt ? rnpt.value.code : null,
    };
  },
};

const isUpperCase = string => /^[A-Z|А-Я]*$/.test(string);

const filterMutations = {
  setRegion(state, region) {
    const name = [...region.name].map(r => (isUpperCase(r) ? r : '')).join('');
    state[FilterCode.REGION] = { name, code: FilterCode.REGION, value: region };
  },
  setTnved(state, tnved) {
    state[FilterCode.TNVED] = { name: tnved.pieceText, code: FilterCode.TNVED, value: tnved };
  },
  setCountry(state, country) {
    state[FilterCode.COUNTRY] = { name: country.pieceText, code: FilterCode.COUNTRY, value: country };
  },
  setEnvironment(state, environment) {
    state[FilterCode.ENVIRONMENT] = { name: environment.pieceText, code: FilterCode.ENVIRONMENT, value: environment };
  },
  setEaes(state, eaes) {
    state[FilterCode.EAES] = { name: eaes.pieceText, code: FilterCode.EAES, value: eaes };
  },
  setDropoutReason(state, reason) {
    state[FilterCode.DROPOUT_REASONS] = { name: reason.pieceCode, code: FilterCode.DROPOUT_REASONS, value: reason };
  },
  setTaxMode(state, mode) {
    state[FilterCode.TAX_MODES] = { name: mode.pieceText, code: FilterCode.TAX_MODES, value: mode };
  },
  setViolation(state, violation) {
    state[FilterCode.VIOLATIONS] = { name: violation.pieceCode, code: FilterCode.VIOLATIONS, value: violation };
  },
  setRisk(state, risk) {
    state[FilterCode.RISKS] = { name: risk.pieceText, code: FilterCode.RISKS, value: risk };
  },
  setConflict(state, conflict) {
    state[FilterCode.CONFLICTS] = { name: conflict.pieceCode, code: FilterCode.CONFLICTS, value: conflict };
  },
  setPenalty(state, penalty) {
    state[FilterCode.PENALTIES] = { name: penalty.pieceText, code: FilterCode.PENALTIES, value: penalty };
  },
  setConflictsResolved(state, conflictsResolved) {
    state[FilterCode.CONFLICTS_RESOLVED] = { name: conflictsResolved.pieceText, code: FilterCode.PENALTIES, value: conflictsResolved };
  },
  setRnpt(state, rnpt) {
    state[FilterCode.RNPT_COUNT] = { name: rnpt.pieceText, code: FilterCode.RNPT_COUNT, value: rnpt };
  },

  removeFilter(state, code) {
    if (!Object.prototype.hasOwnProperty.call(state, code)) {
      return;
    }
    state[code] = null;
  },

  clearFilters(state) {
    Object.keys(state).forEach(key => (state[key] = null));
  },
};

const filterActions = {
  clearFilters({ commit }) {
    commit('clearFilters');
  },
};

export const filterModule = {
  namespaced: true,
  state: filterState,
  getters: filterGetters,
  mutations: filterMutations,
  actions: filterActions,
};
