import BaseService from './base-service';

class RegionInfoService extends BaseService {
  getRegionInfo(rowKeyValues, taxPeriod, countryCode, taxType, violationCode, riskCode) {
    const params = {
      taxPeriod,
      countryCode,
      taxType,
      rowKeyValues,
      violationCode,
      riskCode,
    };
    return this.$http.post('/GetRegionIndicators', params);
  }

  getRegionTableData(rowKeyValues, taxPeriod, countryCode, indicatorCode, taxType, violationCode, riskCode) {
    const params = {
      taxPeriod,
      countryCode,
      taxType,
      rowKeyValues,
      indicatorCode,
      violationCode,
      riskCode,
    };
    return this.$http.post('/GetRegionTaxDivisionsInfo', params);
  }

  getTaxDivisionTableData(rowKeyValues, taxYear, taxPeriodNumber, countryCode, indicatorCode, taxType, violationCode, riskCode) {
    const taxPeriod = (taxYear === 0) ? null : {
      taxYear,
      taxPeriodNumber,
    };
    const params = {
      taxPeriod,
      countryCode,
      taxType,
      rowKeyValues,
      indicatorCode,
      violationCode,
      riskCode,
    };
    return this.$http.post('/GetTaxDivisionInfo', params);
  }

  loadPageTaxpayerTableData(taxYear, taxPeriodNumber, countryCode, indicatorCode, taxType, violationCode, riskCode, inn, pagingData) {
    const taxPeriod = (taxYear === 0) ? null : {
      taxYear,
      taxPeriodNumber,
    };
    const params = {
      taxPeriod,
      countryCode,
      taxType,
      indicatorCode,
      violationCode,
      riskCode,
      inn,
      pagingData,
    };
    return this.$http.post('/LoadPageTaxpayerInfo', params);
  }
}

const regionInfoService = new RegionInfoService();

export default regionInfoService;
