import rgbToHex from '../../../../helpers/rgb-helpers';

export const createSimpleDataModule = (service, loadMethod) => ({
  namespaced: true,

  state() {
    return {
      items: [],
    };
  },
  getters: {
    items(state, getters, rootState, rootGetters) {
      return state.items.map(i => ({ ...i, color: rgbToHex(i.pieceColor), formattedValue: rootGetters['scale/scaledValue'](i.pieceSize) }));
    },
    series(state, getters) {
      return getters.items.map(i => i.pieceSize);
    },
    labels(state, getters) {
      return getters.items.map(i => i.pieceText);
    },
    colors(state, getters) {
      return getters.items.map(i => i.color);
    },
  },
  mutations: {
    setItems(state, items) {
      state.items = items;
    },
  },
  actions: {
    async loadItems({ commit }, filter) {
      const data = await service[loadMethod](filter);
      commit('setItems', data.chartPieces);
    },
  },
});
