import { feature } from 'topojson-client';
import districts from '../../assets/rus_federative.json';
import regions from '../../assets/rus_regions.json';

const geometryState = {
  districts: feature(districts, districts.objects.rus_federative_origin).features,
  regions: feature(regions, regions.objects.rus_regions_origin).features,
};

const getters = {
  regionByDistrictId:
    state => districtId => state.regions.filter(feat => feat.properties.foId === districtId),

  regionById:
    state => regionId => state.regions.filter(feat => feat.properties.regionId === regionId),

  districtById:
    state => foId => state.districts.filter(feat => feat.properties.foId === foId),
};

const mutations = {};

const actions = {};

export default {
  namespaced: true,
  state: geometryState,
  getters,
  mutations,
  actions,
};
