<template>
  <el-card class="fnsd-period-card">
    <div @click="setPeriod(null)" class="fnsd-period-btn-clear">
      За весь период
    </div>
    <el-carousel :loop="false" :autoplay="false" arrow="always" indicator-position="none" trigger="click" height="110px">
      <el-carousel-item v-for="(page, idx) in pagedPeriods" :key="idx">
        <el-row type="flex" align="bottom" class="fnsd-period-row" style="height: 110px;">
          <el-col v-for="(period, i) in page" :key="i" :style="{'width': 100 / pageSize + '%'}">
            <div @click="setPeriod(period)"
                 class="fnsd-period-attr-bar"
                 :style="{ opacity: periodOpacity(period)}"
            >
              <div class="fnsd-period-value">{{period.formattedValue}}</div>
              <div class="fnsd-period-col"
                   :style="period.style"
              ></div>
              <div class="fnsd-period-date">{{period.taxPeriodStr}}</div>
            </div>
          </el-col>
        </el-row>
      </el-carousel-item>
    </el-carousel>
  </el-card>
</template>

<script>
  import { mapMutations, mapState, mapGetters } from 'vuex';
  import taxPeriodsService from '../services/tax-periods-service';
  import { fitColor } from '../helpers/utils';

  export default {
    name: 'Periods',
    data() {
      return {
        periods: [],
        pageSize: 8,
      };
    },
    computed: {
      ...mapState({
        currentPeriod: state => state.period,
      }),

      ...mapState('indicators', {
        currentIndicator: 'currentIndicator',
      }),
      ...mapGetters('scale', {
        scaledValue: 'scaledValue',
      }),

      pagedPeriods() {
        const max = Math.max(...this.periods.map(p => p.indicatorValue));
        const periods = this.periods.map((period) => {
          let indicatorLength = Math.round(parseFloat(period.indicatorValue) / max * 70);
          if (Number.isNaN(indicatorLength) || indicatorLength < 1) {
            indicatorLength = 1;
          }
          return { ...period, indicatorLength, formattedValue: this.scaledValue(period.indicatorValue) };
        });
        fitColor(periods);
        periods.forEach(period => (period.style = this.calculateStyle(period)));

        const paged = [];
        for (let i = 0; i < this.periods.length; i += this.pageSize) {
          paged.push(periods.slice(i, i + this.pageSize));
        }
        return paged;
      },

      periodOpacity() {
        return (period) => {
          if (this.currentPeriod) {
            return this.currentPeriod.id === period.id ? 1 : 0.3;
          }
          return 1;
        };
      },
    },
    watch: {
      currentIndicator() {
        this.loadPeriods();
      },
    },
    created() {
      this.loadPeriods();
    },
    methods: {
      async loadPeriods() {
        this.periods = await taxPeriodsService.getPeriodsByCode(this.currentIndicator.code);
        this.setPeriod(null);
      },

      calculateStyle(item) {
        const height = `height: ${item.indicatorLength}px`;
        return item.regionColor ? `${height}; background-color: rgb(${item.regionColor})` : height;
      },

      ...mapMutations({
        setPeriod: 'SET_PERIOD',
      }),
    },
  };
</script>
