<template>
  <fragment>
    <div v-if="viewChartMode">
      <el-row>
        <el-col :span="12">
          <div class="fnsd-chart-centred">
            <clickable-chart
              label-top="ТОП-5"
              label-bottom="ТНВЭД"
              :series="series"
              :labels="labels"
              :colors="colors"
              :selected="selectedTnvedName"
              @slice:select="updateTnvedSelection"
            ></clickable-chart>
          </div>
        </el-col>

        <el-col :span="12">
          <div class="fnsd-chart-mode">
            <img src="../../images/button-chart-pressed.png">
            <img @click="viewChartMode = false" src="../../images/button-list.png" class="img-active">
          </div>
          <transition name="fade" mode="out-in" >
            <!--<div class="fnsd-chart-legend-column fnsd-chart-legend-column-hover" v-if="!selectedTnved" key="animateList" >-->
            <div class="fnsd-chart-legend-column" v-if="!selectedTnved" key="animateList" >
              <div class="fnsd-chart-legend-item"
                  v-for="(group, index) in legend" :key="index"
              >
                <div class="fnsd-chart-legend-group-title">
                  {{group.name ? group.name : '&nbsp;'}}
                </div>
                <div class="fnsd-chart-legend-item-inner"
                    v-for="tnved in group.pieces"
                    :key="tnved.pieceText"
                    @click="setTnved(tnved)"
                    :style="`opacity: ${selectedTnved ? (selectedTnved.pieceText === tnved.pieceText ? 1 : 0.3) : 1}; cursor: pointer;`"
                >
                  <card :title="tnved.pieceText" :color="tnved.color" :percentage="tnved.pieceSize" :value="tnved.formattedValue"></card>
                </div>
              </div>
            </div>
            <div class="fnsd-col-tnved-decryption" v-if="selectedTnved" key="animateItem">
              <div class="fnsd-chart-legend-column">
                <div class="fnsd-chart-legend-group-title">{{selectedTnved.group}}</div>

                <div class="fnsd-legend-horizontal">
                  <div class="fnsd-legend-point" :style="{ backgroundColor: selectedTnved.color }"></div>
                  <div class="fnsd-legend-title">{{selectedTnved.pieceText}}</div>
                  <div class="fnsd-legend-value-box">
                    <div class="fnsd-legend-percentage">
                      {{selectedTnved.pieceSize}}<span>%</span>
                    </div>
                    <div class="fnsd-legend-value">
                      {{selectedTnved.formattedValue}}
                    </div>
                  </div>
                </div>
              </div>

              <div class="fnsd-tnved-decryption">
                <div class="fnsd-tnved-decryption-item" v-for="row in selectedTnved.rows" :key="row.code">
                  <div class="fnsd-tnved-decryption-title">{{row.code}}</div>
                  <div class="fnsd-tnved-decryption-text">{{row.description}}</div>
                </div>
              </div>
            </div>
          </transition>
        </el-col>
      </el-row>
    </div>
    <div v-else class="fnsd-chart-panel fnsd-chart-tnved">
      <div class="fnsd-chart-mode">
        <img @click="viewChartMode = true" src="../../images/button-chart.png" class="img-active">
        <img src="../../images/button-list-pressed.png">
      </div>

      <div class="fnsd-chart-radio fnsd-chart-radio-compact">
        <el-radio-group v-model="tnvedRadio" class="fnsd-chart-radio-count-3">
          <el-radio-button label="all">Все</el-radio-button>
          <el-radio-button label="top5">ТОП-5</el-radio-button>
          <el-radio-button label="other">Прочие</el-radio-button>
        </el-radio-group>
      </div>

      <div class="fnsd-list-search">
        <el-input
          prefix-icon="el-icon-search"
          v-model="filter">
          <i slot="suffix" @click="filter=''" title="Очистить" class="el-input__icon el-icon-close" style="cursor: pointer"></i>
        </el-input>
      </div>

      <div class="fnsd-chart-table fnsd-chart-table-compact">
        <el-table
          :data="tableData"
          height="340"
          class="custom-table"
          style="width: 100%;"
        >
          <el-table-column width="5px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-point"
                    :style="{'background-color': `${scope.row.color||'rgba(0, 0, 0, 0.1'}`}"
              ></span>
            </template>
          </el-table-column>

          <el-table-column
            label="Логическая группа"
            sortable
            prop="group"
          >
            <template slot-scope="scope">
              <span class="fnsd-table-tnved-td-title">{{scope.row.group}}</span>
            </template>
          </el-table-column>

          <el-table-column
            label="Код ТНВЭД"
            sortable
            prop="pieceText"
          >
            <template slot-scope="scope">
              <span class="fnsd-table-tnved-td-code">{{scope.row.pieceText}}</span>
            </template>
          </el-table-column>

          <el-table-column
            label="Значение в %"
            sortable
            prop="pieceSize">
          </el-table-column>

          <el-table-column
            label="Значение в ₽"
            sortable
            :sort-method="sortValues"
            prop="formattedValue">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </fragment>
</template>

<script>
  import { mapMutations, mapState, mapGetters, mapActions } from 'vuex';
  import Card from '../../components/Card.vue';
  import ClickableChart from './ClickableChart.vue';

  export default {
    name: 'Tnved',
    components: {
      Card,
      ClickableChart,
    },
    data() {
      return {
        tnvedRadio: 'all',
        filter: '',
        viewChartMode: true,
      };
    },
    computed: {
      ...mapState({
        period: state => state.period,
      }),
      ...mapGetters('filter', {
        region: 'region',
        country: 'country',
        environment: 'environment',
        eaes: 'eaes',
        dropoutReason: 'dropoutReason',
        taxMode: 'taxMode',
        violation: 'violation',
        risk: 'risk',
        conflict: 'conflict',
        rnpt: 'rnpt',
        selectedTnved: 'tnved',
        serverFilter: 'serverFilter',
      }),

      ...mapGetters('tnved', {
        legend: 'legend',
        series: 'tnvedSeries',
        labels: 'tnvedLabels',
        colors: 'tnvedColors',
        flatTnveds: 'flatTnveds',
        top5: 'top5',
        others: 'others',
      }),

      selectedTnvedName() {
        return this.selectedTnved ? this.selectedTnved.pieceText : null;
      },

      tableData() {
        const filterStr = this.filter.toLowerCase();
        const predicate = e => e.pieceText.toLowerCase().includes(filterStr) || e.group.toLowerCase().includes(filterStr);
        switch (this.tnvedRadio) {
          case 'all':
            return this.flatTnveds.filter(e => predicate(e));
          case 'top5':
            return this.top5.filter(e => predicate(e));
          case 'other':
            return this.others.filter(e => predicate(e));
          default:
            return [];
        }
      },
    },
    created() {
      this.loadTnveds(this.serverFilter);
      this.$watch(
        vm => [vm.period, vm.region, vm.country, vm.environment, vm.eaes, vm.dropoutReason, vm.taxMode, vm.violation, vm.risk,
          vm.conflict, vm.rnpt].join(),
        () => this.loadTnveds(this.serverFilter),
      );
    },
    methods: {
      ...mapMutations('filter', {
        setTnved: 'setTnved',
      }),

      ...mapActions('tnved', {
        loadTnveds: 'loadTnveds',
      }),

      updateTnvedSelection(name) {
        // const tnved = this.flatTnveds.find(t => t.pieceText === name);
        const tnved = this.legend.map(p => p.pieces).flat().find(t => t.pieceText === name);
        if (tnved) {
          this.setTnved(tnved);
        }
      },

      sortValues(a, b) {
        return a.pieceValue - b.pieceValue;
      },
    },
  };
</script>

<style>
  .apexcharts-datalabels-group {
    display: none;
  }

  .el-radio-button__inner {
    width: 100%;
  }

  .radio3 {
    width: 100%;
    user-select: none;
  }

  .radio3 label {
    width: 33%;
  }

  .cell {
    font-weight: 100;
    white-space: pre !important;
  }

  .custom-table table {
    border-spacing: 0 5px;
  }

  .custom-table td {
    border-top: 1px solid #cbdfef;
    border-bottom: 1px solid #cbdfef;
  }

  .custom-table td:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-left: 1px solid #cbdfef;
  }

  .custom-table td:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-right: 1px solid #cbdfef;
  }

  .custom-table th {
    border-bottom: none !important;
  }
</style>
