import ChartMode from './chart-mode';

const defaults = function defaults(opts) {
  return {
    labels: opts.labels,
    colors: opts.colors,
    chart: opts.chart,

    legend: {
      show: false,
    },
    states: {
      hover: {
        filter: {
          type: 'none',
        },
      },
      active: {
        filter: {
          type: 'none',
        },
      },
    },
    tooltip: {
      enabled: false,
      fillSeriesColor: false,
      y: {
        formatter: value => `${value}%`,
      },
      style: {
        fontSize: '15px',
        fontFamily: '\'IBM Plex Sans\', Helvetica, Arial, sans-serif',
      },
    },
    dataLabels: {
      enabled: true,
      formatter: value => `${parseFloat(value.toFixed(2))}%`,
      style: {
        fontSize: '15px',
        fontFamily: '\'IBM Plex Sans\', Helvetica, Arial, sans-serif',
        colors: ['#ffffff'],
      },
    },
    plotOptions: {
      pie: {
        expandOnClick: false,
      },
    },
    stroke: {
      width: 1,
    },
  };
};

export default function (mode, opts) {
  const options = defaults(opts);

  options.chart.animations = {
      enabled: false,
        easing: 'easeinout',
        speed: 4000,
    };

  switch (mode) {
    case ChartMode.PIE:
      break;

    case ChartMode.DONUT:
      options.plotOptions.pie.donut = {
        size: '50%',
        labels: {
          show: true,
          value: {
            fontFamily: '\'IBM Plex Sans\', Helvetica, Arial, sans-serif',
            formatter: () => 'test',
          },
          total: {
            show: true,
            formatter: () => 'test',
          },
        },
      };
      options.fill = {
        type: 'solid',
      };
      break;

    case ChartMode.GRADIENT_DONUT:
      options.plotOptions.pie.donut = {
        size: '40%',
      };

      options.plotOptions.pie.dataLabels = {
        offset: 45,
        minAngleToShowLabel: 0,
      };

      options.fill = {
        type: 'gradient',
        gradient: {
          shade: 'dark',
          shadeIntensity: 0.2,
          inverseColors: false,
          type: 'vertical',
          opacityFrom: 1,
          opacityTo: 1,
          stops: [70, 98, 100],
        },
      };

      break;

    case ChartMode.RADIAL:
      options.plotOptions = {
        radialBar: {
          offsetY: -10,
          startAngle: 0,
          endAngle: 270,
          hollow: {
            margin: 5,
            size: '30%',
            background: 'transparent',
            image: undefined,
          },
          track: {
            startAngle: 0,
            endAngle: 270,
            background: 'rgba(0, 0, 0, 0.15)',
          },
        },
      };
      options.tooltip = {
        enabled: false,
      };
      break;

    default:
      throw new Error(`Invalid chart mode - ${mode}`);
  }

  return options;
}
