<template>
  <div class="fnsd-measure">
    <div class="fnsd-measure-label">
      <div class="fnsd-measure-label-item" @click="setThousands">
        <span class="fnsd-measure-label-full">Тысячи</span>
        <span class="fnsd-measure-label-short">тыс.</span>
      </div>
      <div class="fnsd-measure-label-item" @click="setMillions">
        <span class="fnsd-measure-label-full">Миллионы</span>
        <span class="fnsd-measure-label-short">млн</span>
      </div>
      <div class="fnsd-measure-label-item" @click="setBillions">
        <span class="fnsd-measure-label-full">Миллиарды</span>
        <span class="fnsd-measure-label-short">млрд</span>
      </div>
    </div>
    <el-progress :showText="false" :percentage="currentScale.position"></el-progress>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
  name: 'ScalePanel',
  computed: {
    ...mapState('scale', {
      currentScale: 'currentScale',
    }),
  },
  methods: {
    ...mapMutations('scale', {
      setThousands: 'setThousands',
      setMillions: 'setMillions',
      setBillions: 'setBillions',
    }),
  },
};
</script>

