<template>
  <fragment>
    <div v-if="chartMode" class="fnsd-chart-panel">
      <el-row>
        <el-col :span="12">
          <div class="fnsd-chart-centred">
            <clickable-chart
              height="350px"
              label-top="РЕЖИМЫ"
              label-bottom=""
              :series="series"
              :labels="labels"
              :colors="colors"
              :selected="selectedTaxModeName"
              @slice:select="updateTaxModeSelection"
            ></clickable-chart>
          </div>

          <div class="fnsd-special-mode">
            СР&nbsp;&mdash; спецрежим
          </div>
        </el-col>

        <el-col :span="12">
          <div class="fnsd-chart-mode">
            <img src="../../images/button-chart-pressed.png">
            <img @click="chartMode = false" src="../../images/button-list.png" class="img-active">
          </div>

          <div class="fnsd-chart-legend-column">
            <div
              class="fnsd-chart-legend-item"
              v-for="mode in items"
              :key="mode.pieceText"
            >
              <div
                class="fnsd-chart-legend-item-inner"
                :style="`opacity: ${selectedMode ? (selectedMode.pieceText === selectedMode.pieceText ? 1 : 0.3) : 1}; cursor: pointer;`"
                @click="setTaxMode(mode)"
              >
                <card direction="horizontalAlt"
                      :title="mode.pieceText"
                      :color="mode.color"
                      :percentage="mode.pieceSize"
                      :value="mode.formattedValue"
                      :special-mode="mode.specialMode ? 'СР' : ''"
                ></card>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <div v-else class="fnsd-chart-panel fnsd-chart-environment">
      <div class="fnsd-chart-mode">
        <img @click="chartMode = true" src="../../images/button-chart.png" class="img-active">
        <img src="../../images/button-list-pressed.png">
      </div>

      <div class="fnsd-chart-radio fnsd-chart-radio-compact">
        <el-radio-group v-model="radio"
                        class="fnsd-chart-radio-count-2"
        >
          <el-radio-button label="Все"></el-radio-button>
          <el-radio-button label="Спецрежимы"></el-radio-button>
        </el-radio-group>
      </div>

      <div class="fnsd-chart-table fnsd-chart-table-large">
        <el-table
          :show-header="false"
          :data="tableData"
          height="340"
          class="custom-table"
        >
          <el-table-column width="7px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-point"
                    :style="{'background-color': `${scope.row.color||'#FFF'}`}"
              ></span>
            </template>
          </el-table-column>

          <el-table-column prop="pieceText" width="220px">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-title">{{scope.row.pieceText}}</span>
            </template>
          </el-table-column>

          <!-- <el-table-column prop="pieceCode"></el-table-column> -->

          <el-table-column prop="pieceSize" width="145px">
            <template slot-scope="scope">
              <span
                class="fnsd-chart-table-percentage fnsd-chart-table-separate"
                :style="{'color': `${scope.row.color||'#000'}`}"
              >{{scope.row.pieceSize}}<span>%</span></span>
            </template>
          </el-table-column>

          <el-table-column prop="formattedValue">
            <template slot-scope="scope">
              <span class="fnsd-chart-table-value">{{scope.row.formattedValue}}<span><!--тут ед. измерения--></span></span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </fragment>
</template>

<script>
  import { mapMutations, mapState, mapGetters, mapActions } from 'vuex';
  import Card from '../../components/Card.vue';
  import ClickableChart from './ClickableChart.vue';

  export default {
    name: 'TaxModes',
    components: {
      Card,
      ClickableChart,
    },
    data() {
      return {
        radio: 'Все',
        chartMode: true,
      };
    },
    computed: {
      ...mapState({
        period: 'period',
      }),

      ...mapGetters('filter', {
        region: 'region',
        tnved: 'tnved',
        environment: 'environment',
        eaes: 'eaes',
        selectedMode: 'taxMode',
        serverFilter: 'serverFilter',
      }),

      ...mapGetters('taxModes', {
        items: 'items',
        series: 'series',
        labels: 'labels',
        colors: 'colors',
      }),

      selectedTaxModeName() {
        return this.selectedMode ? this.selectedMode.pieceText : null;
      },

      tableData() {
        if (this.radio === 'Все') {
          return this.items;
        }
        return this.items.filter(m => !!m.specialMode);
      },
    },
    created() {
      this.loadItems(this.serverFilter);
      this.$watch(vm => [vm.period, vm.region, vm.tnved, vm.environment, vm.eaes].join(), () => this.loadItems(this.serverFilter));
    },
    methods: {
      ...mapMutations('filter', {
        setTaxMode: 'setTaxMode',
        removeFilter: 'removeFilter',
      }),

      ...mapActions('taxModes', {
        loadItems: 'loadItems',
      }),

      updateTaxModeSelection(name) {
        const mode = this.items.find(m => m.pieceText === name);
        if (mode) {
          this.setTaxMode(mode);
        }
      },
    },
  };
</script>

